export default class OverlayAwards {

	static selector = 'overlay-awards';

	el = {};

	uri;

	constructor(container, uri) {
		this.el = {
			container,
			content: container.querySelector(`.${this.constructor.selector}__content`)
		};

		this.uri = uri;
	}

	open() {
		this.render();
	}

	close() {
		this.el.content.innerHTML = '';
	}

	render() {
		fetch('/awards/body.php').then((res) => res.text()).then((html) => {
			this.el.content.scrollTop = 0;
			this.el.content.innerHTML = html;
		});
	}

	destroy() {

	}

}
