export default class Share {

	static selector = 'share';

	el = {};

	links = {};

	uri;
	url;
	title;

	constructor(container) {
		this.el = {
			container,
			anchors: container.querySelectorAll(`.${this.constructor.selector} a`),
			toggle: container.querySelector(`.${this.constructor.selector}__toggle`)
		};

		this.uri = this.el.container.getAttribute('data-share-uri');
		this.url = encodeURIComponent(`https://${window.location.hostname}/${this.uri}`);
		this.title = encodeURIComponent(this.el.container.getAttribute('data-share-title'));

		this.links = {
			twitter: `https://twitter.com/intent/tweet?url=${this.url}`,
			facebook: `https://www.facebook.com/sharer/sharer.php?u=${this.url}`,
			linkedin: `https://www.linkedin.com/shareArticle?title=${this.title}&summary=&source=&url=${this.url}`,
			pinterest: `https://www.pinterest.com/pin/create/bookmarklet/?url=${this.url}&description=${this.title}`,
			mail: `mailto:?body=${this.url}`
		};

		this.addEventListeners();
	}

	addEventListeners() {
		this.el.toggle.addEventListener('click', this.#onToggleClick);

		for (const anchor of this.el.anchors) {
			const platform = anchor.getAttribute('data-share');
			anchor.href = this.links[platform];
			anchor.addEventListener('click', this.#onShareLinkClick);
		}
	}

	removeEventListeners() {
		this.el.toggle.removeEventListener('click', this.#onToggleClick);

		for (const anchor of this.el.anchors) {
			anchor.removeEventListener('click', this.#onShareLinkClick);
		}
	}

	#onToggleClick = (e) => {
		e.preventDefault();
		const classes = this.el.container.classList;
		classes[!classes.contains('is-toggled') ? 'add' : 'remove']('is-toggled');
	}

	#onShareLinkClick = (e) => {
		e.preventDefault();
		this.openWindow(e.currentTarget.href);
	}

	openWindow(href) {
		const w = 500;
		const h = 500;
		const ww = window.innerWidth;
		const wh = window.outerHeight;
		const top = (wh / 2) - (h / 2);
		const left = (ww / 2) - (w / 2);

		window.open(href, '_blank', `location=yes, height=${h}, width=${w}, scrollbars=no, status=yes, top=${top}, left=${left}`);
	}

	destroy() {
		this.removeEventListeners();
	}

}
