import ResizeController from '../../assets/js/controllers/resize';

export default class Gallery {

	static selector = 'gallery';

	el = {};

	constructor(container) {
		this.el = {
			container,
			list: container.querySelector('ol'),
			items: container.querySelectorAll('li')
		};

		this.setHeight();

		this.addEventListeners();
	}

	setHeight() {
		let min = 0;
		for (const image of this.el.items) {
			const sum = image.offsetTop + image.clientHeight;
			if (sum > min) min = sum;
		}
		this.el.list.style.height = `${min}px`;
	}

	#onResize = () => {
		this.setHeight();
	}

	addEventListeners() {
		ResizeController.add(this.#onResize);
	}

	removeEventListeners() {
		ResizeController.remove(this.#onResize);
	}

	destroy() {
		this.removeEventListeners();
	}

}
