import HeightController from '../../../assets/js/controllers/height';
import ResizeController from '../../../assets/js/controllers/resize';

import { getRootOffset } from '../../../assets/js/helpers';

export default class OverviewThinking {

	static selector = 'overview-thinking';

	el = {};

	height = 0;

	constructor(container) {
		this.el = {
			container,
			sticky: {
				container: container.querySelector('[data-sticky-container]'),
				target: container.querySelector('[data-sticky-target]')
			},
			breakSticky: document.querySelector('[data-break-sticky]')
		};

		this.measure();
		this.setHeight();
		this.addEventListeners();
	}

	measure() {
		if (this.el.breakSticky) {
			const height = getRootOffset(this.el.breakSticky.previousSibling).top + this.el.breakSticky.previousSibling.clientHeight - getRootOffset(this.el.sticky.container).top;
			if (height > this.el.sticky.target.clientHeight) this.height = height;
		}
	}

	setHeight() {
		const mq = this.el.container.classList.contains('overview-thinking--applied') ? 1024 : 1280;
		this.el.sticky.container.style.height = this.height && window.matchMedia(`(min-width: ${mq}px)`).matches ? `${this.height}px` : '';
	}

	#onResize = () => {
		this.measure();
		this.setHeight();
	}

	addEventListeners() {
		ResizeController.add(this.#onResize);
		HeightController.add(this.#onResize);
	}

	removeEventListeners() {
		ResizeController.remove(this.#onResize);
		HeightController.remove(this.#onResize);
	}

	destroy() {
		this.removeEventListeners();
	}

}
