import SC from 'soundcloud';

class SoundcloudController {

	clientId = 'c7359510f1c011faac238460f0a694ee';
	userId = '223310691';

	el = {};

	tracks = [];

	players = {};
	active;

	constructor() {
		SC.initialize({
			client_id: this.clientId
		});
	}

	initialize(player) {
		this.el = {
			player,
			cover: player.querySelector('[data-player-cover]'),
			progress: player.querySelector('[data-player-progress]'),
			current: player.querySelector('[data-player-current]'),
			duration: player.querySelector('[data-player-duration]'),
			toggle: player.querySelector('[data-player-toggle]'),
			close: player.querySelector('[data-player-close]'),
		};

		this.addEventListeners();
	}

	async loadTracks() {
		const data = await SC.get(`/users/${this.userId}/tracks`);
		return data;
	}

	trackIndex(url) {
		for (let i = 0, l = this.tracks.length; i < l; ++i) {
			if (this.tracks[i].permalink_url === url) {
				return i;
			}
		}
	}

	async requestPlayer(url) {
		if (!this.tracks.length) this.tracks = await this.loadTracks();
		let player;
		if (this.players[url]) {
			player = this.players[url];
		} else {
			const index = this.trackIndex(url);
			player = this.createPlayer(index);
			this.players[url] = player;
		}
		return player;
	}

	setCover(url, src) {
		const cover = document.createElement('img');
		cover.src = src;
		this.players[url].cover = cover;
	}

	createPlayer(index) {
		const track = this.tracks[index];

		const audio = new Audio();
		audio.preload = 'metadata';
		audio.src = `${track.stream_url}?client_id=${this.clientId}`;
		audio.onplaying = () => {
			if (track.permalink_url !== this.active) {
				if (this.active) {
					this.players[this.active].audio.pause();
					this.deactivate();
				}
				this.activate(track.permalink_url);
			}
			if (track.permalink_url === this.active) {
				this.el.player.classList.add('is-playing');
			}
		};
		audio.onpause = () => {
			if (track.permalink_url === this.active) {
				this.el.player.classList.remove('is-playing');
			}
		};
		audio.ontimeupdate = () => {
			if (track.permalink_url === this.active) {
				this.el.current.innerText = this.formatSeconds(audio.currentTime);
				const duration = this.formatSeconds(audio.duration);
				this.el.duration.innerText = `/${duration}`;
				this.el.progress.style.transform = `scaleX(${audio.currentTime / audio.duration})`;
			}
		};

		return {
			track,
			audio
		};
	}

	activate(url) {
		this.active = url;
		this.el.player.classList.add('is-visible');
		this.el.cover.appendChild(this.players[url].cover.cloneNode());
	}

	deactivate() {
		this.active = null;
		this.el.player.classList.remove('is-visible');
		this.el.cover.innerHTML = '';
	}

	formatSeconds(seconds) {
		let s = parseInt(seconds % 60, 10);
		s = s < 10 ? `0${s}` : s;
		const ns = seconds / 60;
		let m = Math.floor(ns);
		m = m < 10 ? `0${m}` : m;
		return `${m}:${s}`;
	}

	#onToggleClick = () => {
		const player = this.players[this.active];
		player.audio.paused ? player.audio.play() : player.audio.pause();
	}

	#onCloseClick = () => {
		this.players[this.active].audio.pause();
		this.deactivate();
	}

	addEventListeners() {
		this.el.toggle.addEventListener('click', this.#onToggleClick);
		this.el.close.addEventListener('click', this.#onCloseClick);
	}

	removeEventListeners() {
		this.el.toggle.removeEventListener('click', this.#onToggleClick);
		this.el.close.removeEventListener('click', this.#onCloseClick);
	}

}

export default new SoundcloudController();
