import ScrollController from '../../../assets/js/controllers/scroll';


export default class DetailCareers {

	static selector = 'detail-careers';

	el = {};

	constructor(container) {
		this.el = {
			container,
			apply: container.querySelector(`.${this.constructor.selector}__overview .button-pill`),
			form: document.querySelector('.careers-form')
		};

		this.addEventListeners();
	}

	addEventListeners() {
		this.el.apply.addEventListener('click', this.#onApplyClick);
	}

	removeEventListeners() {
		this.el.apply.removeEventListener('click', this.#onApplyClick);
	}

	#onApplyClick = (e) => {
		e.preventDefault();
		const offset = this.el.form.offsetTop - 120;
		ScrollController.scrollTo(offset);
	}

	destroy() {
		this.removeEventListeners();
	}

}
