export default class ListOffices {

	static selector = 'list-offices';

	attributes = {
		timezoneOffset: 'data-timezone-offset'
	};

	el = {};

	date;
	time = 0;
	offset = 0;

	interval;

	constructor(container) {
		this.el = {
			container,
			timezones: container.querySelectorAll('[data-timezone-offset]')
		};

		this.updateClock();

		this.interval = setInterval(() => this.updateClock(), 1000);
	}

	updateClock() {
		this.date = new Date();
		this.time = this.date.getTime();
		this.offset = this.date.getTimezoneOffset();

		for (const timezone of this.el.timezones) {
			const local = new Date(this.time + (this.offset - parseInt(timezone.getAttribute('data-timezone-offset') * 60)) * 60000);
			let hours = local.getHours();
			let noon = 'AM';
			if (hours > 12) {
				hours -= 12;
				noon = 'PM';
			}
			let minutes = local.getMinutes();
			if (minutes < 10) minutes = `0${minutes}`;
			timezone.innerText = `${hours}:${minutes} ${noon}`;
		}
	}

	destroy() {
		clearInterval(this.interval);
	}

}
