import App from '../../../assets/js/app';

export default class IntroCaseStudy {

	static selector = 'intro-case-study';

	el = {};

	constructor(container) {
		this.el = {
			container,
			asset: container.querySelector('.asset')
		};

		if (App.nextAsset) {
			App.nextAsset.classList.add('is-next-asset');
			this.el.asset.replaceWith(App.nextAsset);
			App.nextAsset = null;
		}
	}

	destroy() {

	}

}
