import RoutingController from '../../assets/js/controllers/routing';
import ScrollController from '../../assets/js/controllers/scroll';

export default class WorkFilter {

	static selector = 'case-studies';

	el = {};

	constructor(container) {
		this.el = {
			container,
			anchors: container.querySelectorAll('a')
		};

		this.addEventListeners();
	}

	#onAnchorClick = () => {
		RoutingController.forceProgress(ScrollController.eased);
	}

	addEventListeners() {
		for (const anchor of this.el.anchors) anchor.addEventListener('click', this.#onAnchorClick);
	}

	removeEventListeners() {
		for (const anchor of this.el.anchors) anchor.removeEventListener('click', this.#onAnchorClick);
	}

	destroy() {
		this.removeEventListeners();
	}

}
