import AnimationController from '../../../assets/js/controllers/animation';
import ResizeController from '../../../assets/js/controllers/resize';

import Cursor from '../../../assets/js/components/cursor';

import { getRootOffset } from '../../../assets/js/helpers';

export default class PlayerVideo {

	static selector = 'player-video';

	el = {};

	animation;

	flags = {
		isPlaying: false
	};

	cursor;

	width = 0;
	offset = 0;

	breakpoint;

	duration = 0;
	currentTime = 0;
	progress = 0;

	seeking = false;

	constructor(container) {
		this.el = {
			container,
			stage: container.querySelector(`.${this.constructor.selector}__stage`),
			poster: container.querySelector(`.${this.constructor.selector}__poster`),
			video: container.querySelector(`.${this.constructor.selector}__video video`),
			scrubber: container.querySelector(`.${this.constructor.selector}__scrubber`),
			current: container.querySelector(`.${this.constructor.selector}__current`),
			duration: container.querySelector(`.${this.constructor.selector}__duration`)
		};

		this.measure();

		this.addEventListeners();

		this.breakpoint = window.matchMedia('(min-width: 1280px)');

		if (this.breakpoint.matches) {
			this.cursor = new Cursor(this.el.stage, this.el.stage);
			this.cursor.el.cursor.addEventListener('click', this.#onPosterClick);
		}
	}

	measure() {
		this.width = this.el.stage.offsetWidth;
		this.offset = getRootOffset(this.el.stage).left;
	}

	formatTime(time) {
		const numbers = parseInt(time, 10);

		let minutes = Math.floor(numbers / 60),
			seconds = numbers - (minutes * 60);

		if (minutes < 10) minutes = `0${minutes}`;
		if (seconds < 10) seconds = `0${seconds}`;

		return `${minutes}:${seconds}`;
	}

	start() {
		this.animation = AnimationController.set({
			update: this.#update,
			render: this.#render
		});
	}

	pause() {
		AnimationController.clear(this.animation);
	}

	#update = () => {
		if (this.seeking || this.currentTime !== this.el.video.currentTime) {
			this.currentTime = this.el.video.currentTime;
			this.progress = this.currentTime / this.duration;
			return true;
		}
	}

	#render = () => {
		this.el.current.innerText = this.formatTime(this.currentTime);
		this.el.scrubber.style.transform = `translateX(${this.progress * 100}%)`;
	}

	#onResize = () => {
		this.measure();
	}

	#onDocumentMouseMove = (e) => {
		if (this.seeking) {
			this.el.video.currentTime = ((e.touches ? e.touches[0].pageX : e.clientX) - this.offset) / this.width * this.duration;
		}
	}

	#onDocumentMouseUp = () => {
		if (this.seeking) {
			this.seeking = false;
			this.el.container.classList.remove('is-scrubbing');
		}
	}

	#onVideoLoadedMetaData = () => {
		this.duration = this.el.video.duration;
		this.el.duration.innerText = this.formatTime(this.duration);
	}

	#onVideoPlay = () => {
		this.start();
		this.el.container.classList.add('is-playing');
	}

	#onVideoPause = () => {
		this.pause();
		this.el.container.classList.remove('is-playing');
	}

	#onVideoClick = (e) => {
		e.preventDefault();
		this.el.video.pause();
	}

	#onPosterClick = (e) => {
		e.preventDefault();
		if (!this.duration) this.el.video.load();
		this.el.video.play();
	}

	#onScrubberMouseDown = (e) => {
		e.preventDefault();
		this.seeking = true;
		this.el.video.currentTime = ((e.touches ? e.touches[0].pageX : e.clientX) - this.offset) / this.width * this.duration;
		this.el.container.classList.add('is-scrubbing');
	}

	addEventListeners() {
		ResizeController.add(this.#onResize);

		document.addEventListener('mousemove', this.#onDocumentMouseMove);
		document.addEventListener('mouseup', this.#onDocumentMouseUp);
		document.addEventListener('touchmove', this.#onDocumentMouseMove, { passive: true });
		document.addEventListener('touchend', this.#onDocumentMouseUp);

		this.el.video.addEventListener('loadedmetadata', this.#onVideoLoadedMetaData);
		this.el.video.addEventListener('play', this.#onVideoPlay);
		this.el.video.addEventListener('pause', this.#onVideoPause);
		this.el.video.addEventListener('click', this.#onVideoClick);

		this.el.poster.addEventListener('click', this.#onPosterClick);
		this.el.scrubber.addEventListener('mousedown', this.#onScrubberMouseDown);
		this.el.scrubber.addEventListener('touchstart', this.#onScrubberMouseDown, { passive: true });
	}

	removeEventListeners() {
		ResizeController.remove(this.#onResize);

		document.removeEventListener('mousemove', this.#onDocumentMouseMove);
		document.removeEventListener('mouseup', this.#onDocumentMouseUp);
		document.removeEventListener('touchmove', this.#onDocumentMouseMove);
		document.removeEventListener('touchend', this.#onDocumentMouseUp);

		this.el.video.removeEventListener('loadedmetadata', this.#onVideoLoadedMetaData);
		this.el.video.removeEventListener('play', this.#onVideoPlay);
		this.el.video.removeEventListener('pause', this.#onVideoPause);
		this.el.video.removeEventListener('click', this.#onVideoClick);

		this.el.poster.removeEventListener('click', this.#onPosterClick);
		this.el.scrubber.removeEventListener('mousedown', this.#onScrubberMouseDown);
		this.el.scrubber.removeEventListener('touchstart', this.#onScrubberMouseDown);
	}

	destroy() {
		this.el.video.pause();
		this.removeEventListeners();
		if (this.cursor) {
			this.cursor.el.cursor.removeEventListener('click', this.#onPosterClick);
			this.cursor.destroy();
		}
	}

}
