import AnimationController from '../controllers/animation';
import HeightController from '../controllers/height';
import ResizeController from '../controllers/resize';
import ScrollController from '../controllers/scroll';

import { getRootOffset } from '../helpers';


export default class Cursor {

	el = {};

	animation;

	ease = 0.2;

	bounds = {
		x: null,
		y: null,
		height: null,
		width: null
	};

	coords = {
		current: {
			x: 0,
			y: 0
		},
		last: {
			x: 0,
			y: 0
		},
		scroll: {
			y: 0
		}
	};

	flags = {
		isFirst: true,
		isOverlay: false
	};

	x;
	y;

	timeout;

	constructor(container) {
		this.el = {
			container,
			text: container.querySelector('.carousel__text'),
			cursor: container.querySelector('[data-cursor-takeover]'),
			anchors: container.querySelectorAll('p > a'),
		};
		this.init();
	}

	// LET'S MOSEY

	init() {
		const carousel = this.el.container.closest('.carousel');
		this.flags.isOverlay = carousel ? carousel.hasAttribute('data-dragger-overlay') : false;
		this.measure();
		this.addEventListeners();
	}

	// EVENT LISTENERS

	addEventListeners() {
		ResizeController.add(this.#onResize);
		HeightController.add(this.#onResize);

		this.el.container.addEventListener('mousemove', this.#onMouseMove, { passive: true });
		this.el.container.addEventListener('mousedown', this.#onMouseDown, false);
		this.el.container.addEventListener('mouseup', this.#onMouseUp, false);

		this.el.container.addEventListener('mouseenter', this.#onMouseEnter, false);
		this.el.container.addEventListener('mouseleave', this.#onMouseLeave, false);

		if (this.el.anchors && this.el.anchors.length) {
			for (const anchor of this.el.anchors) {
				anchor.addEventListener('mouseenter', this.#onAnchorEnter, false);
				anchor.addEventListener('mouseleave', this.#onAnchorLeave, false);
			}
		}
	}

	removeEventListeners() {
		ResizeController.remove(this.#onResize);
		HeightController.remove(this.#onResize);

		this.el.container.removeEventListener('mousemove', this.#onMouseMove);
		this.el.container.removeEventListener('mousedown', this.#onMouseDown);
		this.el.container.removeEventListener('mouseup', this.#onMouseUp);

		this.el.container.removeEventListener('mouseleave', this.#onMouseLeave);
		this.el.container.removeEventListener('mouseenter', this.#onMouseEnter);

		if (this.el.anchors && this.el.anchors.length) {
			for (const anchor of this.el.anchors) {
				anchor.removeEventListener('mouseenter', this.#onAnchorEnter, false);
				anchor.removeEventListener('mouseleave', this.#onAnchorLeave, false);
			}
		}
	}

	// LISTENER FUNCTIONS

	#onResize = () => {
		this.measure();
	}

	#onAnchorEnter = (e) => {
		e.preventDefault();
		this.el.cursor.classList.add('is-disabled');
	}

	#onAnchorLeave = (e) => {
		e.preventDefault();
		this.el.cursor.classList.remove('is-disabled');
	}

	#onMouseMove = (e) => {
		this.coords.current.x = e.pageX;
		this.coords.current.y = this.flags.isOverlay ? e.pageY - ScrollController.top : e.pageY;
	}

	#onMouseLeave = () => {
		this.stop();
	}

	#onMouseEnter = (e) => {
		this.coords.current.x = e.pageX;
		this.coords.current.y = this.flags.isOverlay ? e.pageY - ScrollController.top : e.pageY;
		this.start();
	}

	#onMouseDown = () => {
		this.el.cursor.classList.add('is-down');
	}

	#onMouseUp = () => {
		this.el.cursor.classList.remove('is-down');
	}

	// CLASS FUNCTIONS

	measure() {
		const bounds = getRootOffset(this.el.container);
		const offset = this.el.text ? this.el.text.offsetHeight : 0;
		this.bounds.x = bounds.left;
		this.bounds.y = bounds.top + offset;
		this.bounds.height = bounds.height;
		this.bounds.width = window.innerWidth;
	}

	// ANIMATIONS

	start() {
		this.animation = AnimationController.set({
			update: this.#update,
			render: this.#render
		});
	}

	stop() {
		AnimationController.clear(this.animation);
	}

	#update = () => {
		if (this.flags.isFirst) {
			this.flags.isFirst = false;
			this.coords.last.x = this.coords.current.x;
			this.coords.last.y = this.coords.current.y;
		} else {
			this.coords.last.x = this.lerp(this.coords.last.x, this.coords.current.x, this.ease);
			this.coords.last.y = this.lerp(this.coords.last.y, this.coords.current.y, this.ease);
		}
		this.x = this.coords.last.x - this.bounds.x;
		this.y = this.coords.last.y - this.bounds.y;
		return true;
	}

	#render = () => {
		this.el.cursor.style.transform = `translate3d(${this.x}px, ${this.y}px, 0)`;
	}

	// HELPERS

	lerp(a, b, n) {
		return (1 - n) * a + n * b;
	}

	// DESTROY EVERYTHING

	destroy() {
		this.stop();
		this.removeEventListeners();
		this.el.cursor.style = '';
	}

}
