import App from '../../assets/js/app';

import AnimationController from '../../assets/js/controllers/animation';
import HeightController from '../../assets/js/controllers/height';
import ResizeController from '../../assets/js/controllers/resize';
import RoutingController from '../../assets/js/controllers/routing';
import ScrollController from '../../assets/js/controllers/scroll';

import { getRootOffset, parseHydration } from '../../assets/js/helpers';

export default class NextCaseStudy {

	static selector = 'next-case-study';

	el = {};

	animation;

	uri = '/';

	data = {};

	viewport = 0;
	middle = 0;

	offset = 0;
	height = 0;

	progress = 0;
	translate = 0;

	takeover = false;

	constructor(container) {
		this.el = {
			container,
			fixed: container.querySelector(`.${this.constructor.selector}__fixed`),
			currentColor: container.querySelector(`.${this.constructor.selector}__current-color`),
			nextColor: container.querySelector(`.${this.constructor.selector}__next-color`),
			background: container.querySelector(`.${this.constructor.selector}__background`),
			next: container.querySelector('[data-next]')
		};

		this.uri = this.el.container.dataset.uri;

		this.data = parseHydration(this.el.container);

		this.measure();
		this.start();
		this.addEventListeners();
	}

	measure() {
		this.offset = getRootOffset(this.el.fixed).top;
		this.viewport = window.innerHeight;
		this.middle = this.viewport / 2;
		this.height = this.el.container.clientHeight;
	}

	update = () => {
		let progress = (ScrollController.eased + this.middle - this.offset) / (this.height - this.middle);
		if (progress < 0) progress = 0;
		if (progress >= 0.75) {
			this.takeover = true;
			progress = 1;
		}
		if (progress > 1) progress = 1;
		if (progress !== this.progress) {
			this.progress = progress;
			if (ScrollController.eased > this.offset - this.middle) {
				this.translate = ScrollController.eased - this.offset + (this.middle - this.middle * this.progress);
			} else {
				this.translate = 0;
			}
			return true;
		}
	}

	render = () => {
		if (!App.isTouch) {
			if (this.takeover) {
				this.takeover = false;
				this.pause();
				App.freeze();
				this.el.container.classList.add('has-transition');
				this.el.fixed.style.transform = `matrix(1, 0, 0, 1, 0, ${this.translate})`;
				setTimeout(() => {
					App.removeFade();
					App.nextAsset = this.el.container.querySelector('.asset');
					document.documentElement.classList.add('is-next-case-study');
				}, 500);

				setTimeout(() => {
					this.checkNavTransition();
					RoutingController.navigate(`/${this.uri}`);
				}, 800);
			} else {
				this.el.fixed.style.transform = `matrix(1, 0, 0, 1, 0, ${this.translate})`;
				let opacity = this.progress * 2;
				if (opacity > 1) opacity = 1;
				this.el.nextColor.style.opacity = opacity;
			}
		} else {
			let opacity = this.progress * 2;
			if (opacity > 1) opacity = 1;
			this.el.nextColor.style.opacity = opacity;
		}
	}

	start() {
		this.animation = AnimationController.set({
			update: this.update,
			render: this.render
		});
	}

	pause() {
		AnimationController.clear(this.animation);
	}

	#onResize = () => {
		this.measure();
	}

	#onDocumentKeyDown = (e) => {
		switch (e.keyCode) {
			case 37:
				document.documentElement.classList.add('is-next-case-study');
				this.checkNavTransition();
				RoutingController.navigate(`/${this.data.previous}`, false);
				break;
			case 39:
				document.documentElement.classList.add('is-next-case-study');
				this.checkNavTransition();
				RoutingController.navigate(`/${this.data.next}`, false);
				break;
			default:
				break;
		}
	}

	#onNextClick = () => {
		App.removeFade();
		App.nextAsset = this.el.container.querySelector('.asset');
		this.checkNavTransition();

		RoutingController.navigate(`/${this.uri}`);
	}

	checkNavTransition() {
		if (ScrollController.top > document.documentElement.querySelector('main > header').offsetHeight) {
			document.documentElement.classList.add('is-scroll-transition');
			AnimationController.one({
				update: () => true,
				render: () => {
					document.documentElement.classList.remove('is-scroll-transition');
				}
			});
		}
	}

	addEventListeners() {
		ResizeController.add(this.#onResize);
		HeightController.add(this.#onResize);

		document.addEventListener('keydown', this.#onDocumentKeyDown);

		this.el.next.addEventListener('click', this.#onNextClick);
	}

	removeEventListeners() {
		ResizeController.remove(this.#onResize);
		HeightController.remove(this.#onResize);

		document.removeEventListener('keydown', this.#onDocumentKeyDown);

		this.el.next.removeEventListener('click', this.#onNextClick);
	}

	destroy() {
		this.pause();
		this.removeEventListeners();
	}

}
