import XHR from '../../assets/js/utils/xhr';


export default class NewsletterForm {

	static selector = 'newsletter-form';

	el = {};

	constructor(container) {
		this.el = {
			container,
			form: container.querySelector(`.${this.constructor.selector} form`),
			input: container.querySelector(`.${this.constructor.selector} input[type="email"]`),
			honeypot: container.querySelector(`.${this.constructor.selector} input[name="b_4f0c09aa0d3c6025719bd5819_eba2b1b148"]`),
			button: container.querySelector(`.${this.constructor.selector} button[type="submit"]`),
			error: container.querySelector(`.${this.constructor.selector}__response-error`),
			success: container.querySelector(`.${this.constructor.selector}__response-success`)
		};

		this.addEventListeners();
	}

	addEventListeners() {
		this.el.form.addEventListener('submit', this.#onFormSubmit);
	}

	removeEventListeners() {
		this.el.form.removeEventListener('submit', this.#onFormSubmit);
	}

	#onFormSubmit = (e) => {
		e.preventDefault();
		this.hideError();

		if (this.el.honeypot.value) {
			this.showSuccess();
			this.el.button.disabled = true;
			return;
		}

		this.el.input.value === '' || !this.validate(this.el.input.value) ? this.showError() : this.submitForm();
	}

	validate(email) {
		return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
	}

	showSuccess() {
		this.el.success.style.display = 'block';
	}

	hideSuccess() {
		this.el.success.style.display = 'none';
	}

	showError() {
		this.el.error.style.display = 'block';
	}

	hideError() {
		this.el.error.style.display = 'none';
	}

	submitForm() {
		const data = new FormData(this.el.form);

		return new Promise((resolve) => {
			new XHR('json')
				.post('/php/newsletter-form.php', data)
				.then(xhr => {
					if (xhr.response.status === 'subscribed') {
						this.hideError();
						this.showSuccess();
						this.el.button.disabled = true;
						if (window.ga) window.ga('send', 'event', 'subscribe', 'click', 'newsletter_contact');
						resolve();
					} else {
						this.hideSuccess();
						this.showError();
						this.el.button.disabled = false;
					}
				});
		});
	}

	destroy() {
		this.removeEventListeners();
	}

}
