import App from '../../../assets/js/app';

import { parseHydration } from '../../../assets/js/helpers';

export default class OverlayTeam {

	static selector = 'overlay-team';

	el = {};

	uri;

	data;

	index;

	constructor(container, uri) {
		this.el = {
			container,
			content: container.querySelector(`.${this.constructor.selector}__content`),
			previous: container.querySelector(`.${this.constructor.selector}__previous`),
			next: container.querySelector(`.${this.constructor.selector}__next`)
		};

		this.uri = uri;

		this.data = parseHydration(container);
	}

	open(content) {
		for (let i = 0, l = this.data.length; i < l; ++i) {
			if (this.data[i].slug === content) {
				this.render(i);
				break;
			}
		}
	}

	close() {
		this.el.content.innerHTML = '';
	}

	render(index) {
		const member = this.data[index];
		this.index = index;

		fetch(`/team/${member.slug}/body.php`).then((res) => res.text()).then((html) => {
			this.el.content.scrollTop = 0;
			this.el.content.innerHTML = html;

			this.el.content.querySelector('.asset').dataset.render = true;

			let previous = this.index - 1;
			if (previous < 0) previous = this.data.length - 1;
			this.el.previous.setAttribute('href', `/${this.uri}/${this.data[previous].slug}`);

			let next = this.index + 1;
			if (next >= this.data.length) next = 0;
			this.el.next.setAttribute('href', `/${this.uri}/${this.data[next].slug}`);

			App.apply(this.el.content, this.uri);
			App.refresh(true);
		});
	}

	destroy() {

	}

}
