import AnimationController from '../../../assets/js/controllers/animation';
import HeightController from '../../../assets/js/controllers/height';
import ResizeController from '../../../assets/js/controllers/resize';

import { getRootOffset } from '../../../assets/js/helpers';

export default class ListAwards {

	static selector = 'list-awards';

	el = {};

	animation;

	active;

	translate = [0, 0];
	current = [0, 0];
	last = [0, 0];

	bounds = {
		offsetTop: 0,
		imageWidth: 0,
		imageOffset: 0
	};

	constructor(container) {
		this.el = {
			container,
			list: container.querySelector('ul'),
			items: container.querySelectorAll('ul a'),
			images: container.querySelector(`.${this.constructor.selector}__images`),
			assets: container.querySelectorAll(`.${this.constructor.selector}__images li`)
		};

		this.measure();
		this.addEventListeners();
	}

	measure() {
		this.bounds.offsetTop = getRootOffset(this.el.list).top;
		this.bounds.imageWidth = this.el.images.offsetWidth;
		this.bounds.imageOffset = this.bounds.imageWidth * 0.5 + 80;
	}

	start() {
		this.animation = AnimationController.set({
			update: this.#update,
			render: this.#render
		});
	}

	stop() {
		AnimationController.clear(this.animation);
	}

	#update = () => {
		this.last = [this.lerp(this.last[0], this.current[0], 0.15), this.lerp(this.last[1], this.current[1], 0.15)];
		const translate = [this.last[0], this.last[1]];
		if (translate[0] !== this.translate[0] || translate[1] !== this.translate[1]) {
			this.translate = translate;
			return true;
		}
	}

	#render = () => {
		this.el.images.style.transform = `matrix(1, 0, 0, 1, ${this.translate[0]}, ${this.translate[1]})`;
	}

	#onResize = () => {
		this.measure();
	}

	#onDocumentMouseMove = (e) => {
		this.current = [e.pageX - this.bounds.imageOffset, e.pageY - this.bounds.offsetTop - this.bounds.imageOffset];
	}

	#onListMouseEnter = (e) => {
		this.current = [e.pageX - this.bounds.imageOffset, e.pageY - this.bounds.offsetTop - this.bounds.imageOffset];
		this.start();
	}

	#onListMouseLeave = () => {
		// this.el.container.classList.remove('is-up');
		this.stop();
	}

	#onItemMouseEnter = (e) => {
		const wasActive = this.active;

		for (let i = 0, l = this.el.items.length; i < l; ++i) {
			this.el.assets[i].classList.remove('is-inactive');
			this.el.assets[i].classList.remove('is-active');

			if (this.el.items[i] === e.currentTarget) {
				this.active = i;
			}
		}

		wasActive > this.active ? this.el.container.classList.add('is-up') : this.el.container.classList.remove('is-up');

		if (wasActive !== undefined && !this.el.assets[wasActive].classList.contains('is-inactive')) {
			this.el.assets[wasActive].classList.add('is-inactive');
		}

		if (!this.el.assets[this.active].classList.contains('is-active')) {
			this.el.assets[this.active].classList.add('is-active');
		}
	}

	addEventListeners() {
		ResizeController.add(this.#onResize);
		HeightController.add(this.#onResize);

		document.addEventListener('mousemove', this.#onDocumentMouseMove);

		this.el.list.addEventListener('mouseenter', this.#onListMouseEnter);
		this.el.list.addEventListener('mouseleave', this.#onListMouseLeave);

		for (const item of this.el.items) item.addEventListener('mouseenter', this.#onItemMouseEnter);
	}

	removeEventListeners() {
		ResizeController.remove(this.#onResize);
		HeightController.remove(this.#onResize);

		document.removeEventListener('mousemove', this.#onDocumentMouseMove);

		this.el.list.removeEventListener('mouseenter', this.#onListMouseEnter);
		this.el.list.removeEventListener('mouseleave', this.#onListMouseLeave);

		for (const item of this.el.items) item.removeEventListener('mouseenter', this.#onItemMouseEnter);
	}

	lerp(a, b, n) {
		return (1 - n) * a + n * b;
	}

	destroy() {
		AnimationController.clear(this.animation);
		this.removeEventListeners();
	}

}
