import AnimationController from './animation';
import HeightController from './height';
import ResizeController from './resize';
import ScrollController from './scroll';

import { getRootOffset } from '../helpers';

class ColorController {

	attributes = {
		backgroundColor: 'data-background-color',
		textColor: 'data-text-color',
		theme: 'data-theme'
	};

	amimation;

	default = {
		background: 'var(--wild-sand)',
		text: 'var(--tuatara)'
	};

	theme = {
		'var(--wild-sand)': 'dark',
		'#f4f4f4': 'dark',
		'var(--azalea)': 'dark',
		'#f9cdcd': 'dark',
		'var(--tuatara)': 'bright',
		'#252422': 'bright'
	};

	colors = [];

	current = {
		background: null,
		text: null
	};

	initialize() {
		this.animation = AnimationController.set({
			update: this.update,
			render: this.render
		});

		ResizeController.add(this.#onResize);
		HeightController.add(this.#onResize);
	}

	gatherColors() {
		this.colors = [];
		const colors = document.querySelectorAll(`[${this.attributes.backgroundColor}][${this.attributes.textColor}]`);
		for (const el of colors) {
			this.colors.push({
				el,
				offset: 0,
				background: el.dataset.backgroundColor,
				text: el.dataset.textColor
			});
		}
	}

	measure() {
		for (const color of this.colors) {
			color.offset = getRootOffset(color.el).top;
		}
	}

	setColor(colors = this.current) {
		document.documentElement.style.setProperty('--background-color', colors.background);
		document.documentElement.style.setProperty('--text-color', colors.text);
	}

	setFooterTheme() {
		let theme = this.theme[this.default.text];
		const color = this.colors[this.colors.length - 1];
		if (color) theme = this.theme[color.text];
		document.documentElement.setAttribute(this.attributes.theme, theme);
	}

	refresh(gather) {
		if (gather) {
			this.gatherColors();
			this.setFooterTheme();
		}

		this.measure();
	}

	update = () => {
		let { background, text } = this.default;
		for (const color of this.colors) {
			if (ScrollController.eased + ScrollController.viewport * 0.5 > color.offset) {
				({ background, text } = color);
			}
		}
		if (background !== this.current.background || text !== this.current.text) {
			this.current.background = background;
			this.current.text = text;
			return true;
		}
	}

	render = () => {
		this.setColor();
	}

	#onResize = () => {
		this.measure();
	}

}

export default new ColorController();
