export default class DetailBody {

	static selector = 'detail-body';

	el = {};
	bounds = {};

	highlights = [];

	timeout;

	constructor(container) {
		this.el = {
			container,
			footnotes: container.querySelector(`.${this.constructor.selector}__footnotes ol`),
			highlights: container.querySelectorAll('.highlight'),
			captions: container.querySelectorAll('.caption'),
			blockquotes: container.querySelectorAll('.blockquote'),

			// External elements
			overview: document.querySelector('.overview-blog'),
			intro: document.querySelector('.intro-blog')
		};

		if (this.el.captions) this.wrapInnerText(this.el.captions);
		if (this.el.blockquotes) this.wrapInnerText(this.el.blockquotes);
		this.addEventListeners();
	}

	addEventListeners() {
		if (this.el.highlights) {
			let counter = 0;
			for (const highlight of this.el.highlights) {
				const caption = highlight.querySelector('.caption');
				if (caption) {
					highlight.addEventListener('mouseenter', this.#onHighlightMouseEnter);
					highlight.addEventListener('mouseleave', this.#onHighlightMouseLeave);

					++counter;
					highlight.innerHTML += `<sup>${counter}</sup>`;
					this.el.footnotes.innerHTML += `<li>${caption.innerHTML}</li>`;
				}
			}
		}
	}

	removeEventListeners() {
		if (this.el.highlights) {
			for (const highlight of this.el.highlights) {
				if (highlight.children) {
					highlight.removeEventListener('mouseenter', this.#onHighlightMouseEnter);
					highlight.removeEventListener('mouseleave', this.#onHighlightMouseLeave);
				}
			}
		}
	}

	wrapInnerText(arr) {
		arr.forEach(item => {
			const wrap = document.createElement('span');

			if (item.firstChild) {
				wrap.innerHTML = item.firstChild.textContent;
				item.remove(item.firstChild);
				item.insertAdjacentElement('afterbegin', wrap);
			}
		});
	}

	#onHighlightMouseEnter = (e) => {
		this.clearTimeouts();
		const target = e.currentTarget;
		for (const highlight of this.el.highlights) highlight.classList.remove('is-active');
		if (!target.classList.contains('is-active')) target.classList.add('is-active');
	};

	#onHighlightMouseLeave = (e) => {
		const target = e.currentTarget;
		this.timeout = setTimeout(() => target.classList.remove('is-active'), 500);
	};

	clearTimeouts() {
		if (this.timeout) clearTimeout(this.timeout);
		this.timeout = null;
	}

	destroy() {
		this.removeEventListeners();
		this.clearTimeouts();
	}

}
