import OverlayController from '../../../assets/js/controllers/overlay';

export default class OverviewCaseStudy {

	static selector = 'overview-case-study';

	el = {};

	uri;

	constructor(container, uri) {
		this.el = {
			container,
			openAwardsList: container.querySelector('[data-open-awards-list]')
		};

		this.uri = uri;

		this.addEventListeners();
	}

	#onOpenAwardsList = (e) => {
		e.preventDefault();
		OverlayController.open(`${this.uri}/awards`);
	}

	addEventListeners() {
		if (this.el.openAwardsList) this.el.openAwardsList.addEventListener('click', this.#onOpenAwardsList);
	}

	removeEventListeners() {
		if (this.el.openAwardsList) this.el.openAwardsList.addEventListener('click', this.#onOpenAwardsList);
	}

	destroy() {
		this.removeEventListeners();
	}

}
