import AnimationController from '../controllers/animation';
import ResizeController from '../controllers/resize';

import { getRootOffset } from '../helpers';

export default class Waveform {

	container;

	animation;

	initialized = false;

	canvas;
	context;

	ul;
	lis = [];
	spans = [];

	dimensions = [];
	offset = 0;

	url;
	image;
	peaks = [];

	bar = {
		width: 3,
		count: 50
	};

	bars = [];
	max = 0;

	progress;

	onseeking;

	constructor(url) {
		this.url = url;
	}

	async initialize() {
		if (!this.initialized) {
			this.initialized = true;
			await this.loadImage()
				.then(() => this.scrapeWaveformData())
				.then(() => this.transformWaveformData());
		}
	}

	async loadImage() {
		return new Promise((resolve, reject) => {
			this.image = new Image();
			this.image.onload = () => resolve();
			this.image.onerror = reject;
			this.image.src = `/php/remote-file.php?url=${this.url}`;
		});
	}

	async scrapeWaveformData() {
		const width = this.image.naturalWidth;
		const height = this.image.naturalHeight;
		const center = height / 2;
		const canvas = document.createElement('canvas');
		const context = canvas.getContext('2d');
		canvas.width = width;
		canvas.height = height;
		context.drawImage(this.image, 0, 0);
		const { data } = context.getImageData(0, 0, width, height);
		this.peaks = [];
		for (let w = 0; w < width; ++w) {
			for (let h = 0; h < height; ++h) {
				const i = w * 4 + h * (width * 4);
				if (!data[i + 3]) {
					this.peaks.push((center - h) / center);
					break;
				}
			}
		}
	}

	transformWaveformData() {
		const units = this.peaks.length / this.bar.count;
		this.bars = [];
		for (let b = 0; b < this.bar.count; ++b) {
			let chunk = 0;
			for (let u = 0; u < units; ++u) {
				chunk += this.peaks[b * units + u];
			}
			const total = (chunk ** 3) / (units ** 3);
			this.bars.push(total);
			if (total > this.max) this.max = total;
		}
	}

	setProgress(progress = this.progress) {
		const bars = this.spans.length * progress;
		const full = Math.floor(bars);
		const single = bars - full;
		for (let i = 0, l = this.spans.length; i < l; ++i) {
			if (i < full) {
				this.spans[i].style.transform = 'scaleY(1)';
			} else if (i === full) {
				this.spans[i].style.transform = `scaleY(${single})`;
			} else {
				this.spans[i].style.transform = 'scaleY(0)';
			}
		}
	}

	measure() {
		const width = this.container.clientWidth;
		const height = this.container.clientHeight;
		this.dimensions = [width, height];
		this.offset = getRootOffset(this.ul).left;
	}

	resize(dimensions) {
		if (this.canvas) {
			const [width, height] = dimensions;
			this.canvas.width = width;
			this.canvas.height = height;
		}
	}

	update = () => {
		if (this.cursor) {
			let progress = (this.cursor - this.offset) / this.dimensions[0];
			if (progress < 0) progress = 0;
			if (progress > 1) progress = 1;
			if (progress !== this.progress) {
				this.progress = progress;
				return true;
			}
		}
	}

	render = () => {
		this.setProgress();
		// TODO
		if (this.onseeking) this.onseeking(this.progress);
	}

	start() {
		this.animation = AnimationController.set({
			update: this.update,
			render: this.render
		});
	}

	pause() {
		AnimationController.clear(this.animation);
	}

	async renderHTML(container) {
		this.ul = document.createElement('ul');
		this.container = container;
		this.container.appendChild(this.ul);

		this.measure();
		this.start();
		this.addEventListeners();

		for (let b = 0; b < this.bar.count; ++b) {
			const li = document.createElement('li');
			const span = document.createElement('span');
			this.lis.push(li);
			this.spans.push(span);
			li.appendChild(span);
			this.ul.appendChild(li);
		}

		await this.initialize();

		for (let b = 0; b < this.bar.count; ++b) {
			this.lis[b].style.transform = `scaleY(${this.bars[b] / this.max})`;
		}
	}

	async renderCanvas(width, height) {
		await this.initialize();

		this.canvas = document.createElement('canvas');
		this.context = this.canvas.getContext('2d');

		this.resize([width, height]);

		const size = (width - this.bar.width) / (this.bar.count - 1);
		this.context.fillStyle = getComputedStyle(document.documentElement).getPropertyValue('--text-color');
		for (let i = 0; i < this.bar.count; ++i) {
			const h = height * (this.bars[i] / this.max);
			this.context.fillRect(i * size, height / 2 - h / 2, this.bar.width, h);
		}
	}

	#onResize = () => {
		this.measure();
	}

	#onWaveformMouseDown = (e) => {
		this.cursor = (e.touches ? e.touches[0].pageX : e.clientX);
	}

	#onDocumentMouseMove = (e) => {
		if (this.cursor) {
			this.cursor = (e.touches ? e.touches[0].pageX : e.clientX);
		}
	}

	#onDocumentMouseUp = () => {
		if (this.cursor) this.cursor = null;
	}

	addEventListeners() {
		ResizeController.add(this.#onResize);

		document.addEventListener('mousemove', this.#onDocumentMouseMove);
		document.addEventListener('mouseup', this.#onDocumentMouseUp);
		document.addEventListener('touchmove', this.#onDocumentMouseMove, { passive: true });
		document.addEventListener('touchend', this.#onDocumentMouseUp);

		this.ul.addEventListener('mousedown', this.#onWaveformMouseDown);
		this.ul.addEventListener('touchstart', this.#onWaveformMouseDown, { passive: true });
	}

	removeEventListeners() {
		ResizeController.remove(this.#onResize);

		document.removeEventListener('mousemove', this.#onDocumentMouseMove);
		document.removeEventListener('mouseup', this.#onDocumentMouseUp);
		document.removeEventListener('touchmove', this.#onDocumentMouseMove);
		document.removeEventListener('touchend', this.#onDocumentMouseUp);

		this.ul.removeEventListener('mousedown', this.#onWaveformMouseDown);
		this.ul.removeEventListener('touchstart', this.#onWaveformMouseDown);
	}

	destroy() {
		this.pause();
		this.removeEventListeners();
	}

}
