import PopUpController from '../../../assets/js/controllers/pop-up';

import GDPR from '../../../assets/js/components/gdpr';

export default class PopUpGDPR {

	static selector = 'pop-up-gdpr';

	el = {};

	gdpr;

	status = 0;

	constructor(container) {
		this.el = {
			container,
			accept: container.querySelector('[data-cookies-accept]'),
			reject: container.querySelector('[data-cookies-reject]')
		};

		this.gdpr = new GDPR((isConsent) => {
			if (!isConsent) {
				PopUpController.show(this.el.container);
				this.addEventListeners();
			}
		});
	}

	#onAcceptClick = (e) => {
		e.preventDefault();
		this.gdpr.accept();
		PopUpController.hide(this.el.container);
	}

	#onRejectClick = (e) => {
		e.preventDefault();
		this.gdpr.reject();
		PopUpController.hide(this.el.container);
	}

	addEventListeners() {
		this.el.accept.addEventListener('click', this.#onAcceptClick);
		this.el.reject.addEventListener('click', this.#onRejectClick);
	}

	removeEventListeners() {
		this.el.accept.removeEventListener('click', this.#onAcceptClick);
		this.el.reject.removeEventListener('click', this.#onRejectClick);
	}

	destroy() {
		this.removeEventListeners();
	}

}
