import PopUpController from '../../../assets/js/controllers/pop-up';

export default class PopUpContact {

	static selector = 'pop-up-contact';

	el = {};

	timeout;

	constructor(container) {
		this.el = {
			container,
			close: container.querySelector('[data-close]')
		};

		// setTimeout(() => {
		// 	PopUpController.show(this.el.container);
		// }, 60000);

		// this.resetTimer();
		// this.addEventListeners();
	}

	resetTimer() {
		if (this.timeout) {
			clearTimeout(this.timeout);
			this.timeout = null;
		}
		this.timeout = setTimeout(() => PopUpController.show(this.el.container), 20000);
	}

	#onDocumentMouseMove = () => this.resetTimer()

	#onDocumentScroll = () => this.resetTimer()

	#onDocumentKeydown = () => this.resetTimer()

	#onCloseClick = (e) => {
		e.preventDefault();
		PopUpController.hide(this.el.container);
		this.destroy();
	}

	addEventListeners() {
		// document.addEventListener('mousemove', this.#onDocumentMouseMove);
		// document.addEventListener('scroll', this.#onDocumentScroll);
		// document.addEventListener('keydown', this.#onDocumentKeydown);
		this.el.close.addEventListener('click', this.#onCloseClick);
	}

	removeEventListeners() {
		// document.removeEventListener('mousemove', this.#onDocumentMouseMove);
		// document.removeEventListener('scroll', this.#onDocumentScroll);
		// document.removeEventListener('keydown', this.#onDocumentKeydown);
		this.el.close.removeEventListener('click', this.#onCloseClick);
	}

	destroy() {
		if (this.timeout) clearTimeout(this.timeout);
		this.removeEventListeners();
	}

}
