import { twig } from 'twig';

import App from '../../assets/js/app';

import RoutingController from '../../assets/js/controllers/routing';
import ScrollController from '../../assets/js/controllers/scroll';

import listItemAccordionTemplate from '../../bits/list-items/accordion/template.twig';


import { parseHydration } from '../../assets/js/helpers';


export default class Accordion {

	static selector = 'accordion';

	static offices = ['all', 'bay-area', 'san-diego', 'st-louis', 'remote'];

	el = {};

	uri;

	data = {};
	mapped = {};
	active = {
		office: 'all',
		department: null
	};

	constructor(container, uri) {
		this.el = {
			container,
			filters: container.querySelectorAll(`.${this.constructor.selector}__top-bar button`),
			items: container.querySelectorAll(`.${this.constructor.selector}__content > ul > li`)
		};

		this.uri = uri;

		this.data = parseHydration(container);

		this.mapData();

		this.addEventListeners();
	}

	dashCase(string) {
		return string.replace(/[^0-9a-z ]/gi, '').replace(/ /g, '-').toLowerCase();
	}

	mapData() {
		this.mapped.jobs = {};
		this.mapped.departments = {};
		this.mapped.offices = {};
		for (const job of this.data.jobs) this.mapped.jobs[job.id] = job;
		for (const department of this.data.departments) {
			department.key = this.dashCase(department.name);
			this.mapped.departments[department.id] = department;
		}
		for (const office of this.data.offices) {
			const key = this.dashCase(office.name);
			office.key = key;
			this.mapped.offices[key] = office;
		}
	}

	renderItems() {
		const departments = this.active.office && this.active.office !== 'all' ? this.mapped.offices[this.active.office].departments : this.data.departments;

		for (let i = 0, l = departments.length; i < l; ++i) {
			if (departments[i].id !== 0) {
				const count = this.el.items[i].querySelector(`.${this.constructor.selector}__count`);
				const panel = this.el.items[i].querySelector(`.${this.constructor.selector}__panel`);
				count.innerText = departments[i].jobs ? departments[i].jobs.length : 0;
				const jobs = [];
				if (departments[i].jobs) {
					for (const job of departments[i].jobs) jobs.push(this.mapped.jobs[job.id]);
				}
				panel.innerHTML = twig({ data: listItemAccordionTemplate }).render({ jobs });
				App.apply(panel, this.uri);
			}
		}
	}

	filter(office, department) {
		this.active = {
			office: office || 'all',
			department
		};

		for (const option of this.el.filters) {
			const checked = option.dataset.value === this.active.office;
			option.setAttribute('data-checked', checked);
			option.setAttribute('aria-checked', checked);
		}

		this.renderItems();

		for (const item of this.el.items) {
			const panel = item.querySelector(`.${this.constructor.selector}__panel`);
			const content = item.querySelector(`.${this.constructor.selector}__panel > div`);
			const button = item.querySelector(`.${this.constructor.selector} button`);

			if (department === item.dataset.department) {
				item.classList.add('is-open');
				panel.style.height = `${content.clientHeight}px`;
				panel.setAttribute('aria-hidden', 'false');
				button.setAttribute('aria-expanded', 'true');
			} else {
				item.classList.remove('is-open');
				panel.style.height = '';
				panel.setAttribute('aria-hidden', 'true');
				button.setAttribute('aria-expanded', 'false');
			}
		}
	}

	#onOptionClick = (e) => {
		e.preventDefault();
		const { value } = e.currentTarget.dataset;
		let url = '/careers';
		if (value !== 'all' || this.active.department) url += `/${value}`;
		if (this.active.department) url += `/${this.active.department}`;
		RoutingController.navigate(url, false);
	}

	#onItemClick = (e) => {
		e.preventDefault();
		const id = parseInt(e.currentTarget.closest('li').dataset.id);
		for (const department of this.data.departments) {
			if (id === department.id) {
				let url = '/careers';
				const key = this.dashCase(department.name);
				if (key !== this.active.department) {
					url += `/${this.active.office}/${key}`;
				} else if (this.active.office !== 'all') {
					url += `/${this.active.office}`;
				}
				RoutingController.navigate(url, false);
				break;
			}
		}
	}

	#onItemTransitionEnd = (e) => {
		if (e.propertyName === 'height') ScrollController.adjust();
	}

	addEventListeners() {
		for (const option of this.el.filters) option.addEventListener('click', this.#onOptionClick);
		for (const item of this.el.items) {
			item.querySelector(`.${this.constructor.selector}__panel`).addEventListener('transitionend', this.#onItemTransitionEnd);
			item.querySelector('button').addEventListener('click', this.#onItemClick);
		}
	}

	removeEventListeners() {
		for (const option of this.el.filters) option.removeEventListener('click', this.#onOptionClick);
		for (const item of this.el.items) {
			item.querySelector(`.${this.constructor.selector}__panel`).removeEventListener('transitionend', this.#onItemTransitionEnd);
			item.querySelector('button').removeEventListener('click', this.#onItemClick);
		}
	}

	destroy() {
		this.removeEventListeners();
	}

}
