import AnimationController from '../../../assets/js/controllers/animation';
import HeightController from '../../../assets/js/controllers/height';
import ResizeController from '../../../assets/js/controllers/resize';
import ScrollController from '../../../assets/js/controllers/scroll';

import { getRootOffset } from '../../../assets/js/helpers';

export default class IntroBlog {

	static selector = 'intro-blog';

	animation;

	el = {};

	viewport = 0;

	bounds = {
		asset: 0
	};

	offset = {
		line: 0,
		asset: 0
	};

	difference = 0;

	progress = 0;
	translate = 0;

	constructor(container) {
		this.el = {
			container,
			wrapper: container.querySelector(`.${this.constructor.selector}__wrapper`),
			readTime: container.querySelector(`.${this.constructor.selector} [data-read-time]`),

			// External elements
			asset: container.querySelector('.asset'),
			image: container.querySelector('.asset__wrapper'),
			line: container.querySelector('.meta-bar__line'),
			content: document.querySelector('.detail-body__content')
		};

		this.measure();
		this.setHeight();

		this.calculateReadTime();

		this.animation = AnimationController.set({
			update: this.update,
			render: this.render
		});

		this.addEventListeners();
	}

	measure() {
		this.viewport = window.innerHeight;

		this.bounds.asset = this.el.asset.clientHeight;

		this.offset.line = getRootOffset(this.el.line).top;
		this.offset.asset = getRootOffset(this.el.asset).top;

		this.difference = (this.offset.line - this.bounds.asset) / 2 + (this.offset.asset - this.offset.line) + this.bounds.asset;
	}

	setHeight() {
		this.el.container.style.height = `${this.offset.asset + this.difference - (this.offset.asset - this.offset.line)}px`;
	}

	calculateReadTime() {
		// Average words per minute
		const wpm = 100;
		const words = this.el.content.innerText.split(' ').length;
		let result,
			value;

		if (words) {
			value = Math.ceil(words / wpm);
			result = `${value} Min`;
		}

		this.el.readTime.innerText = result;
	}

	update = () => {
		let progress = ScrollController.eased / this.viewport;
		if (progress < 0) progress = 0;
		if (progress > 1.5) progress = 1.5;
		let translate = ScrollController.eased;
		if (translate > this.difference) translate = this.difference;
		if (translate !== this.translate || progress !== this.progress) {
			this.progress = progress;
			this.translate = translate;
			return true;
		}
	}

	render = () => {
		this.el.wrapper.style.transform = `matrix(1, 0, 0, 1, 0, ${this.translate})`;
		this.el.image.style.transformOrigin = `50% ${100 - this.progress * 100}%`;
	}

	#onResize = () => {
		this.measure();
		this.setHeight();
	}

	addEventListeners() {
		ResizeController.add(this.#onResize);
		HeightController.add(this.#onResize);
	}

	removeEventListeners() {
		ResizeController.remove(this.#onResize);
		HeightController.remove(this.#onResize);
	}

	destroy() {
		AnimationController.clear(this.animation);
		this.removeEventListeners();
	}

}
