import AnimationController from '../controllers/animation';
import HeightController from '../controllers/height';
import ResizeController from '../controllers/resize';

import Cursor from './cursor';

import { getRootOffset } from '../helpers';


export default class Dragger {

	el = {};

	animation;

	options = {
		ease: 0.05,
		speed: 4.5
	};

	flags = {
		isDragging: false,
		isMouseDown: false,
		isProgress: false,
		isOverlay: false
	};

	bounds = {
		offsetLeft: 0,
		totalWidth: 0,
		visibleWidth: 0,
		slideWidth: 0,
		slideHeight: 0
	};

	mousedownX = 0;
	mouseupX = 0;

	currentX = 0;
	lastX = 0;

	currentProgressX = 0;
	lastProgressX = 0;

	min = 0;
	max = 0;

	anchorX = 0;
	height = 0;

	activeIndex = 1;
	total = 0;

	breakpoint;
	cursor;

	timeouts = {
		mouse: null,
		press: null,
		interval: null
	};

	constructor(container) {
		this.el = {
			container,
			wrap: container.querySelector('.carousel__wrap'),
			stage: container.querySelector('.carousel__stage'),
			list: container.querySelector('ul') || container.querySelector('ol'),
			slides: [...container.querySelectorAll('li')],
			anchors: [...container.querySelectorAll('a')],

			// Progress related elements
			current: container.querySelector('.carousel__current'),
			total: container.querySelector('.carousel__total'),
			progressBg: container.querySelector('.carousel__progress-background'),
			progressBar: container.querySelector('.carousel__progress-bar')
		};

		this.flags.isProgress = container.hasAttribute('data-dragger-progress');
		this.flags.isOverlay = container.hasAttribute('data-dragger-overlay');

		this.breakpoint = window.matchMedia('(min-width: 1280px)');
		this.breakpoint.matches ? this.init() : this.initProgress();

		this.addBreakpointListener();
	}

	init() {
		this.measure();
		this.setup();
		this.addEventListeners();
		this.initCursor();
	}

	initCursor() {
		this.cursor = new Cursor(this.el.wrap);
	}

	initProgress() {
		if (this.flags.isProgress && this.el.container.getAttribute('data-dragger-progress') === 'qty') {
			this.currentX = 0;
			this.total = this.el.slides.length;

			this.timeouts.interval = setInterval(() => {
				const current = this.el.stage.scrollLeft;
				const end = this.el.stage.scrollWidth;
				this.progress(current, end);
			}, 15);
		}
	}

	measure() {
		this.bounds.offsetLeft = getRootOffset(this.el.stage).left;
		this.bounds.slideWidth = this.el.slides[0].offsetWidth;
		this.bounds.totalWidth = this.el.slides.length * this.bounds.slideWidth;
		this.bounds.visibleWidth = this.el.container.clientWidth;
	}

	setup() {
		const { visibleWidth, totalWidth, offsetLeft, slideWidth } = this.bounds; // eslint-disable-line
		const gridPadding = 10;

		this.total = this.el.slides.length - 1;

		for (const slide of this.el.slides) {
			const height = slide.clientHeight;
			if (height > this.height) this.height = height;
		}

		this.el.stage.style.height = `${this.height}px`;

		// TODO: Hardcoded paddings
		if (this.flags.isProgress) {
			if (this.el.container.getAttribute('data-dragger-progress') === 'qty') {
				this.max = -(totalWidth + offsetLeft - visibleWidth + (window.innerWidth / 2 - gridPadding) - (slideWidth / 2));
			} else {
				this.max = -(totalWidth + offsetLeft - visibleWidth + 60);
			}
		} else if (this.flags.isOverlay) {
			this.max = -(totalWidth - visibleWidth + 200);
		} else {
			this.max = -(totalWidth + offsetLeft - visibleWidth + 60);
		}

		this.anchorX = offsetLeft;
	}

	// LISTENERS

	addEventListeners() {
		ResizeController.add(this.#onResize);
		HeightController.add(this.#onResize);

		this.el.wrap.addEventListener('mousemove', this.#onMouseMove, { passive: true });
		this.el.wrap.addEventListener('mousedown', this.#onMouseDown, false);
		this.el.wrap.addEventListener('mouseup', this.#onMouseUp, false);
		this.el.wrap.addEventListener('mouseleave', this.#onMouseLeave, false);
		this.el.wrap.addEventListener('mouseenter', this.#onMouseEnter, false);

		if (this.el.anchors && this.el.anchors.length) {
			for (const anchor of this.el.anchors) {
				anchor.addEventListener('dragstart', this.#onAnchorDrag);
				anchor.addEventListener('click', this.#onAnchorClick);
			}
		}
	}

	removeEventListeners() {
		ResizeController.remove(this.#onResize);
		HeightController.remove(this.#onResize);

		this.el.wrap.removeEventListener('mousemove', this.#onMouseMove);
		this.el.wrap.removeEventListener('mousedown', this.#onMouseDown);
		this.el.wrap.removeEventListener('mouseup', this.#onMouseUp);
		this.el.wrap.removeEventListener('mouseleave', this.#onMouseLeave);
		this.el.wrap.removeEventListener('mouseenter', this.#onMouseEnter);

		if (this.el.anchors && this.el.anchors.length) {
			for (const anchor of this.el.anchors) {
				anchor.removeEventListener('dragstart', this.#onAnchorDrag);
				anchor.removeEventListener('click', this.#onAnchorClick);
			}
		}
	}

	addBreakpointListener() {
		this.breakpoint.addListener(this.#onBreakpointResize);
	}

	removeBreakpointListener() {
		this.breakpoint.removeListener(this.#onBreakpointResize);
	}

	// EVENTS

	#onResize = () => {
		this.measure();
		this.setup();
	}

	#onBreakpointResize = (e) => {
		this.toggleMode(e);
	}

	#onAnchorClick = (e) => {
		if (this.flags.isMouseDown) {
			this.flags.isMouseDown = false;
			e.stopPropagation();
			e.preventDefault();
		}
	}

	#onAnchorDrag = (e) => {
		e.preventDefault();
	}

	#onMouseMove = (e) => {
		this.move(e);
	}

	#onMouseDown = (e) => {
		// Prevent firefox dragging images
		e.preventDefault();
		this.drag(e);
	}

	#onMouseUp = () => {
		this.undrag();
		this.el.container.classList.remove('is-disabled');
	}

	#onMouseLeave = () => {
		this.timeouts.mouse = setTimeout(() => this.disable(), 1500);
	}

	#onMouseEnter = () => {
		this.disable();
		this.start();
	}

	// METHODS

	toggleMode(e) {
		if (this.timeouts.mouse) clearTimeout(this.timeouts.mouse);
		if (e.matches) {
			this.init();
		} else {
			this.destroyCursor();
			this.stop();
			this.removeEventListeners();
			this.clearStyles();
			this.initProgress();
		}
	}

	progress(current, end) {
		if (this.flags.isProgress) {
			if (this.el.container.getAttribute('data-dragger-progress') === 'qty') {
				const index = ((current / end) * this.total + 1).toFixed();
				if (this.activeIndex !== index) {
					this.activeIndex = index;
					this.el.current.innerHTML = index.padStart(2, '0');
				}
			} else if (this.el.container.getAttribute('data-dragger-progress') === 'bar') {
				this.currentProgressX = (current / end) * (this.el.progressBg.offsetWidth - this.el.progressBar.offsetWidth);
			}
		}
	}

	checkIsProgressBar() {
		if (this.flags.isProgress && this.el.container.getAttribute('data-dragger-progress') === 'bar') return true;
		return false;
	}

	move(e) {
		if (!this.flags.isDragging) return;
		const userX = e.changedTouches ? e.changedTouches[0].clientX : e.clientX;
		this.currentX = this.mouseupX + (userX - this.mousedownX) * this.options.speed;
		this.clamp();
		this.progress(this.currentX, this.max);
	}

	drag(e) {
		this.flags.isDragging = true;
		const userX = e.changedTouches ? e.changedTouches[0].clientX : e.clientX;
		this.mousedownX = userX;
	}

	undrag() {
		this.flags.isDragging = false;
		this.mouseupX = this.currentX;
	}

	// ANIMATIONS

	start() {
		this.animation = AnimationController.set({
			update: this.#update,
			render: this.#render
		});
	}

	stop() {
		AnimationController.clear(this.animation);
	}

	disable() {
		if (this.timeouts.mouse) clearTimeout(this.timeouts.mouse);
		this.undrag();
		this.stop();
	}

	#update = () => {
		this.lastX = this.lerp(this.lastX, this.currentX, this.options.ease);
		this.lastX = Math.floor(this.lastX * 100) / 100;
		if (this.checkIsProgressBar()) this.lastProgressX = this.lerp(this.lastProgressX, this.currentProgressX, this.options.ease);
		return true;
	}

	#render = () => {
		this.el.stage.style.transform = `translate3d(${this.lastX}px, 0, 0)`;
		if (this.checkIsProgressBar()) this.el.progressBar.style.transform = `translateX(${this.lastProgressX}px)`;
	}

	// HELPERS

	lerp(a, b, n) {
		return (1 - n) * a + n * b;
	}

	clamp() {
		this.currentX = Math.max(Math.min(this.currentX, this.min), this.max);
	}

	// DESTROY EVERYTHING

	clearStyles() {
		this.el.stage.style = '';
	}

	clearTimeouts() {
		if (this.timeouts.mouse) clearTimeout(this.timeouts.mouse);
		if (this.timeouts.press) clearTimeout(this.timeouts.press);
		if (this.timeouts.interval) clearTimeout(this.timeouts.interval);
	}

	destroyCursor() {
		if (this.cursor) {
			this.cursor.destroy();
			this.cursor = null;
		}
	}

	destroy() {
		this.clearTimeouts();
		this.clearStyles();
		this.stop();
		this.destroyCursor();
		this.removeEventListeners();
		this.removeBreakpointListener();
	}

}
