import AnimationController from '../../assets/js/controllers/animation';
import OverlayController from '../../assets/js/controllers/overlay';
import ResizeController from '../../assets/js/controllers/resize';
import ScrollController from '../../assets/js/controllers/scroll';

export default class Header {

	static selector = 'header';

	el = {};

	animation;

	height = 0;

	eased = 0;
	translate = 0;

	constructor(container) {
		this.el = {
			container,
			anchors: container.querySelectorAll('nav a'),
			menu: container.querySelector(`.${this.constructor.selector}__menu`)
		};

		this.measure();

		this.start();
		this.addEventListeners();
	}

	measure() {
		this.height = this.el.container.offsetHeight;
	}

	update = () => {
		let { translate } = this;
		const eased = ScrollController.eased * 1.5;
		const difference = eased - this.eased;
		this.eased = eased;
		if (this.eased < 0) this.eased = 0;
		translate += difference;
		if (translate < 0) translate = 0;
		if (translate > this.height) translate = this.height;
		if (translate !== this.translate) {
			this.translate = translate;
			ScrollController.sticky.offset = this.height - translate;
			return true;
		}
	}

	render = () => {
		this.el.container.style.transform = `matrix(1, 0, 0, 1, 0, -${this.translate})`;
	}

	start() {
		this.animation = AnimationController.set({
			update: this.update,
			render: this.render
		});
	}

	pause() {
		AnimationController.clear(this.animation);
	}

	activate(page) {
		for (const anchor of this.el.anchors) {
			anchor.classList[anchor.dataset.page === page ? 'add' : 'remove']('is-active');
		}
	}

	#onResize = () => {
		this.measure();
	}

	#onMenuClick = (e) => {
		e.preventDefault();
		OverlayController.open('menu');
	}

	addEventListeners() {
		ResizeController.add(this.#onResize);
		this.el.menu.addEventListener('click', this.#onMenuClick);
	}

	removeEventListeners() {
		ResizeController.remove(this.#onResize);
		this.el.menu.removeEventListener('click', this.#onMenuClick);
	}

	destroy() {
		this.pause();
		this.removeEventListeners();
	}

}
