export default class MobileComp {

	static selector = 'mobile-comp';

	el = {};

	frameColor;

	constructor(container) {
		this.el = {
			container,
			frames: container.querySelectorAll('.asset__wrapper')
		};

		this.frameColor = container.dataset.frameColor;

		if (this.frameColor) this.renderFrameColor();
	}

	renderFrameColor() {
		for (const frame of this.el.frames) {
			frame.style.color = this.frameColor;
		}
	}

	destroy() {

	}

}
