export default class ButtonMore {

	static selector = 'button-more';

	el = {};

	observer;

	visible = false;
	loading = false;

	onload;

	timeout;

	constructor(button) {
		this.el = {
			button
		};

		this.observer = new IntersectionObserver(this.#onObservation);
		this.observer.observe(this.el.button);

		this.addEventListeners();
	}

	async load() {
		if (this.onload && !this.loading) {
			this.loading = true;
			this.el.button.disabled = true;
			this.el.button.classList.add('is-loading');
			await this.onload();
			this.loading = false;
			this.el.button.disabled = false;
			this.el.button.classList.add('is-loading');
		}
	}

	show() {
		this.visible = true;
		this.el.button.classList.add('is-visible');
	}

	hide() {
		this.visible = false;
		this.el.button.classList.remove('is-visible');
	}

	#onObservation = (entries) => {
		if (entries[0].isIntersecting && entries[0].boundingClientRect.y > 0 && !this.timeout) {
			// this.timeout = setTimeout(() => {
			// 	this.timeout = null;
			// }, 2000);
			this.load();
		}
	}

	#onButtonClick = (e) => {
		e.preventDefault();
		this.load();
	}

	addEventListeners() {
		this.el.button.addEventListener('click', this.#onButtonClick);
	}

	removeEventListeners() {
		this.el.button.removeEventListener('click', this.#onButtonClick);
	}

	destroy() {
		this.observer.unobserve(this.el.button);
		this.observer.disconnect();
		this.removeEventListeners();
	}

}
