import XHR from '../../assets/js/utils/xhr';


export default class CareersForm {

	static selector = 'careers-form';

	el = {};

	valid;
	timeout;

	constructor(container) {
		this.el = {
			container,
			form: container.querySelector(`.${this.constructor.selector} form`),
			inputs: container.querySelectorAll(`.${this.constructor.selector} input[required]`),
			file: container.querySelector(`.${this.constructor.selector} input[type="file"]`),
			submit: container.querySelector(`.${this.constructor.selector} button[type="submit"]`)
		};

		this.valid = false;
		this.isFileUpload = false;

		this.addEventListeners();
	}

	// LISTENERS

	addEventListeners() {
		this.el.form.addEventListener('submit', this.#onFormSubmit);
		if (this.el.file) {
			this.isFileUpload = true;
			this.el.file.addEventListener('change', this.#onFileInputChange);
		}

		for (const input of this.el.inputs) {
			input.addEventListener('focusin', this.#onInputFocusIn);
			input.addEventListener('focusout', this.#onInputFocusOut);
			input.addEventListener('change', this.#onInputChange);
		}
	}

	removeEventListeners() {
		this.el.form.removeEventListener('submit', this.#onFormSubmit);
		if (this.el.file) this.el.file.removeEventListener('change', this.#onFileInputChange);

		for (const input of this.el.inputs) {
			input.removeEventListener('focusin', this.#onInputFocusIn);
			input.removeEventListener('focusout', this.#onInputFocusOut);
			input.removeEventListener('change', this.#onInputChange);
		}
	}

	// EVENTS

	#onInputChange = (e) => {
		e.currentTarget.parentElement.classList.add('is-focus');
	}

	#onInputFocusIn = (e) => {
		e.currentTarget.parentElement.parentElement.classList.remove('is-error');
		e.currentTarget.parentElement.classList.add('is-focus');
	}

	#onInputFocusOut = (e) => {
		if (!e.currentTarget.value) e.currentTarget.parentElement.classList.remove('is-focus');
	}

	#onFileInputChange = (e) => {
		const label = e.target.nextElementSibling;
		const parent = e.currentTarget.parentElement.parentElement;
		const filename = e.target.value.split('\\').pop();

		if (filename) {
			const ext = filename.split('.').pop().toLowerCase();

			if (ext === 'pdf') {
				parent.classList.remove('is-error');
				label.textContent = filename;
			} else {
				e.target.value = '';
				parent.classList.add('is-error');
				label.textContent = '*Please upload a PDF only';
			}
		}
	}

	#onFormSubmit = (e) => {
		e.preventDefault();
		if (this.checkValidity() === this.el.inputs.length) {
			this.el.submit.classList.remove('is-error');
			this.el.submit.textContent = 'Sending...';
			this.submitForm();
		} else {
			this.el.submit.classList.remove('is-submitting');
			this.el.submit.classList.add('is-error');
			this.el.submit.textContent = 'Fix the errors above and try again please.';
		}
	}

	// METHODS

	checkValidity() {
		let validInputs = 0;

		for (const input of this.el.inputs) {
			if (!input.validity.valid || input.validity.typeMismatch) {
				input.parentElement.parentElement.classList.remove('is-valid');
				input.parentElement.parentElement.classList.add('is-error');

				if (input.type === 'email' && input.value) {
					input.value = '';
					input.placeholder = '*Please enter a valid email address';
				}
			} else {
				validInputs++;
				input.parentElement.parentElement.classList.remove('is-error');
				input.parentElement.parentElement.classList.add('is-valid');
			}
		}

		return validInputs;
	}

	submitForm() {
		this.el.submit.disabled = true;
		const data = new FormData(this.el.form);
		const endpoint = this.isFileUpload ? '/php/careers-form-file.php' : '/php/careers-form.php';

		return new Promise((resolve) => {
			new XHR('json')
				.post(endpoint, data)
				.then(xhr => {
					if (xhr.status === 200) {
						this.el.submit.classList.add('is-submitting');
						this.el.submit.textContent = 'Thank you for your submission. We will be in touch.';
						this.timeout = setTimeout(() => {
							this.el.submit.classList.remove('is-submitting');
							this.el.submit.textContent = 'Submit';
							this.clearForm();
							this.el.submit.disabled = false;
							clearTimeout(this.timeout);
						}, 10000);
						resolve();
					}
				});
		});
	}

	clearForm() {
		for (const input of this.el.inputs) {
			input.parentElement.classList.remove('is-focus');
			if (input.type === 'file') {
				const label = input.nextElementSibling;
				label.textContent = 'Click to upload your resume / portfolio...';
			}
			input.value = '';
		}
	}

	destroy() {
		this.removeEventListeners();
	}

}
