import { twig } from 'twig';

import App from '../../../assets/js/app';

import RoutingController from '../../../assets/js/controllers/routing';
import ScrollController from '../../../assets/js/controllers/scroll';
import SectionController from '../../../assets/js/controllers/section';

import ButtonMore from '../../../bits/buttons/more/script';
import Header from '../../header/script';

import { parseHydration, getRootOffset } from '../../../assets/js/helpers';

import listItemCaseStudyTemplate from '../../../bits/list-items/case-study/template.twig';
import assetTemplate from '../../../bits/asset/template.twig';
import buttonRadioTemplate from '../../../bits/buttons/radio/template.twig';

export default class GridWorkFilter {

	static selector = 'grid-case-studies';

	pageCount = 0;

	el = {};

	uri;

	buttonMore;

	data;
	mapped = {};
	active;

	isOpen = false;

	length = 0;
	page = 0;

	constructor(container, uri) {
		this.el = {
			container,
			wrapper: container.querySelector(`.${this.constructor.selector}__wrapper`),
			sidebar: container.querySelector(`.${this.constructor.selector}__sidebar`),
			optionsHeader: container.querySelector(`.${this.constructor.selector}__options-header`),
			value: container.querySelector(`.${this.constructor.selector}__value`),
			total: container.querySelector(`.${this.constructor.selector}__total`),
			optionsList: container.querySelector(`.${this.constructor.selector}__options-list`),
			services: container.querySelector('[data-category="services"]'),
			industries: container.querySelector('[data-category="industries"]'),
			list: container.querySelector(`.${this.constructor.selector}__content ul`)
		};

		this.uri = uri;

		this.buttonMore = SectionController.classSync(ButtonMore);

		this.data = parseHydration(container);
		this.length = this.data.length;

		this.map('services', 'categoriesServices');
		this.map('industries', 'categoriesIndustries');

		this.renderOptions();

		this.addEventListeners();
	}

	map(category, set) {
		for (let i = 0, l = this.length; i < l; ++i) {
			for (const item of this.data[i][set]) {
				const uri = `${category}/${item.slug}`;
				if (!this.mapped[uri]) {
					this.mapped[uri] = {
						...item,
						category,
						items: [i]
					};
				} else {
					this.mapped[uri].items.push(i);
				}
			}
		}
	}

	renderOptions() {
		this.el.services.innerHTML = '';
		this.el.industries.innerHTML = '';
		for (const uri in this.mapped) {
			const li = document.createElement('li');
			li.innerHTML = twig({ data: buttonRadioTemplate }).render({ value: uri, label: this.mapped[uri].title });
			this.el[this.mapped[uri].category].appendChild(li);
			this.mapped[uri].button = li.querySelector('button');
		}
	}

	renderItems(from, to) {
		for (let i = from; i < to; ++i) {
			const item = this.active ? this.data[this.mapped[this.active].items[i]] : this.data[i];
			item.image = twig({ data: assetTemplate }).render(item.thumbnail.length ? item.thumbnail[0] : {});
			item.body = `<p>${item.description}</p>`;
			const li = document.createElement('li');
			li.innerHTML = twig({ data: listItemCaseStudyTemplate }).render(item);
			this.el.list.appendChild(li);
		}

		App.apply(this.el.list, this.uri);
		App.refresh(true);
	}

	nextPage() {
		const from = this.page;
		let to = this.page + (this.pageCount || this.length);
		if (to > this.length) to = this.length;
		this.renderItems(from, to);
		this.page = to;
		this.buttonMore[this.page < this.length ? 'show' : 'hide']();
	}

	filter(value) {
		this.active = this.mapped[value] ? value : null;
		this.length = this.active ? this.mapped[this.active].items.length : this.data.length;
		this.el.value.innerHTML = this.active ? this.mapped[this.active].title : 'All';
		this.el.total.innerHTML = this.length;
		for (const option in this.mapped) {
			const { button } = this.mapped[option];
			const checked = button.dataset.value === this.active;
			button.setAttribute('data-checked', checked);
			button.setAttribute('aria-checked', checked);
		}
		if (this.length) this.buttonMore.load();
	}

	clearList() {
		App.destroy(this.el.list);
		this.el.list.innerHTML = '';
		this.page = 0;
	}

	open() {
		this.isOpen = true;
		this.el.sidebar.classList.add('is-open');
		const height = this.el.container.querySelector(`.${this.constructor.selector}__options-list > div`).clientHeight;
		this.el.optionsList.style.height = `${height}px`;
	}

	close() {
		this.isOpen = false;
		this.el.sidebar.classList.remove('is-open');
		this.el.optionsList.style.height = '';
	}

	#onButtonMoreLoad = () => {
		this.nextPage();
	}

	#onOptionsHeaderClick = (e) => {
		e.preventDefault();
		!this.isOpen ? this.open() : this.close();
	}

	#onOptionsListTransitionEnd = () => {
		ScrollController.adjust();
	}

	#onOptionClick = (e) => {
		e.preventDefault();
		this.clearList();
		const { value } = e.currentTarget.dataset;
		const offset = getRootOffset(this.el.wrapper).top - SectionController.classSync(Header).height;
		RoutingController.forceProgress(offset < ScrollController.eased ? offset : ScrollController.eased);
		RoutingController.navigate(value !== this.active ? `/${this.uri}/${value}` : `/${this.uri}`, false);
		if (this.isOpen) this.close();
	}

	addEventListeners() {
		this.buttonMore.onload = this.#onButtonMoreLoad;
		this.el.optionsHeader.addEventListener('click', this.#onOptionsHeaderClick);
		this.el.optionsList.addEventListener('transitionend', this.#onOptionsListTransitionEnd);
		for (const uri in this.mapped) this.mapped[uri].button.addEventListener('click', this.#onOptionClick);
	}

	removeEventListeners() {
		delete this.buttonMore.onload;
		this.el.optionsHeader.removeEventListener('click', this.#onOptionsHeaderClick);
		this.el.optionsList.removeEventListener('transitionend', this.#onOptionsListTransitionEnd);
		for (const uri in this.mapped) this.mapped[uri].button.removeEventListener('click', this.#onOptionClick);
	}

	destroy() {
		this.removeEventListeners();
	}

}
