
import Dragger from '../../assets/js/components/dragger';


export default class Menu {

	static selector = 'menu';

	el = {};
	dragger;
	timeout;

	constructor(container) {
		this.el = {
			container,
			extend: container.querySelector(`.${this.constructor.selector}__extend`),
			back: container.querySelector(`.${this.constructor.selector}__back`)
		};

		this.addEventListeners();
	}

	open() {
		this.dragger = new Dragger(this.el.container.querySelector('.carousel'));
	}

	close() {
		if (this.timeout) clearTimeout(this.timeout);
		this.timeout = setTimeout(() => {
			this.dragger.destroy();
		}, 250);
	}

	#onExtendClick = (e) => {
		e.preventDefault();
		this.el.container.classList.add('is-extended');
	}

	#onBackClick = (e) => {
		e.preventDefault();
		this.el.container.classList.remove('is-extended');
	}

	addEventListeners() {
		this.el.extend.addEventListener('click', this.#onExtendClick);
		this.el.back.addEventListener('click', this.#onBackClick);
	}

	removeEventListeners() {
		this.el.extend.removeEventListener('click', this.#onExtendClick);
		this.el.back.removeEventListener('click', this.#onBackClick);
	}

	destroy() {
		if (this.dragger) this.dragger.destroy();
		this.removeEventListeners();
	}

}
