import { twig } from 'twig';

import App from '../../../assets/js/app';

import RoutingController from '../../../assets/js/controllers/routing';
import ScrollController from '../../../assets/js/controllers/scroll';
import SectionController from '../../../assets/js/controllers/section';

import ButtonMore from '../../../bits/buttons/more/script';

import { parseHydration } from '../../../assets/js/helpers';

import listItemThinkingTemplate from '../../../bits/list-items/thinking/template.twig';
import assetTemplate from '../../../bits/asset/template.twig';
import buttonRadioTemplate from '../../../bits/buttons/radio/template.twig';


export default class ListThinkingFilter {

	static selector = 'list-thinking-filter';

	pageCount = 0;

	el = {};

	uri;

	buttonMore;

	data;
	categories = {};
	active;

	length = 0;
	page = 0;

	constructor(container, uri) {
		this.el = {
			container,
			wrapper: container.querySelector(`.${this.constructor.selector}__wrapper`),
			categories: container.querySelector(`.${this.constructor.selector}__top-bar ul`),
			list: container.querySelector(`.${this.constructor.selector}__content ul`),
			buttonMore: container.querySelector(`.${ButtonMore.selector}`)
		};

		this.uri = uri;

		this.buttonMore = SectionController.find(this.el.buttonMore);
		this.buttonMore.class.onload = () => this.nextPage();

		this.data = parseHydration(container);
		this.length = this.data.length;

		this.mapCategories();
		this.renderCategories();

		if (this.length) {
			this.addEventListeners();
		}
	}

	mapCategories() {
		this.categories = {};
		for (let i = 0, l = this.length; i < l; ++i) {
			for (const industry of this.data[i].categoriesThinking) {
				if (!this.categories[industry.slug]) {
					this.categories[industry.slug] = {
						title: industry.title,
						items: [i]
					};
				} else {
					this.categories[industry.slug].items.push(i);
				}
			}
		}
	}

	renderCategories() {
		this.el.categories.innerHTML = '';
		const all = document.createElement('li');
		all.innerHTML = twig({ data: buttonRadioTemplate }).render({ value: 'all', label: 'View All' });
		this.el.categories.appendChild(all);
		for (const slug in this.categories) {
			const li = document.createElement('li');
			li.innerHTML = twig({ data: buttonRadioTemplate }).render({ value: slug, label: this.categories[slug].title });
			this.el.categories.appendChild(li);
		}
	}

	findAppliedIndex(slug) {
		let index = 0;
		for (let i = this.data.length - 1; i > 0; --i) {
			if (this.data[i].categoriesThinking[0].slug === 'applied') ++index;
			if (this.data[i].slug === slug) {
				return index;
			}
		}
	}

	renderItems(from, to) {
		for (let i = from; i < to; ++i) {
			const item = this.active !== 'all' ? this.data[this.categories[this.active].items[i]] : this.data[i];
			item.index = this.findAppliedIndex(item.slug);
			item.image = twig({ data: assetTemplate }).render(item.asset[0] || { width: 1, height: 1 });
			const li = document.createElement('li');
			li.innerHTML = twig({ data: listItemThinkingTemplate }).render(item);
			this.el.list.appendChild(li);
		}

		App.apply(this.el.list, this.uri);
		App.refresh(true);
	}

	nextPage() {
		const from = this.page;
		let to = this.page + (this.pageCount || this.length);
		if (to > this.length) to = this.length;
		this.renderItems(from, to);
		this.page = to;
		this.buttonMore.class[this.page < this.length ? 'show' : 'hide']();
		App.refresh();
	}

	filter(value) {
		this.active = this.categories[value] ? value : 'all';
		this.length = this.active !== 'all' ? this.categories[this.active].items.length : this.data.length;
		for (const option of this.el.categories.querySelectorAll('button')) {
			const checked = option.dataset.value === this.active;
			option.setAttribute('data-checked', checked);
			option.setAttribute('aria-checked', checked);
		}
		if (this.length) this.buttonMore.class.load();
	}

	clearList() {
		App.destroy(this.el.list);
		this.el.list.innerHTML = '';
		this.page = 0;
	}

	// Event methods

	#onOptionClick = (e) => {
		e.preventDefault();
		this.clearList();
		const { value } = e.currentTarget.dataset;
		RoutingController.forceProgress(ScrollController.eased);
		RoutingController.navigate(value === this.active || value === 'all' ? `/${this.uri}` : `/${this.uri}/categories/${value}`, false);
	}

	// Event listeners

	addEventListeners() {
		for (const option of this.el.categories.querySelectorAll('button')) option.addEventListener('click', this.#onOptionClick);
	}

	removeEventListeners() {
		for (const option of this.el.categories.querySelectorAll('button')) option.removeEventListener('click', this.#onOptionClick);
	}

	destroy() {
		this.removeEventListeners();
	}

}
