import ScrollController from './scroll';

class PopUpController {

	visible;

	queue = [];

	show(el) {
		if (this.visible) {
			this.queue.push(el);
		} else {
			if (el !== this.visible && !el.dataset.interacted) {
				this.visible = el;
				el.classList.add('is-visible');
				document.documentElement.style.setProperty('--bottom-panel-height', `${el.clientHeight}px`);
				ScrollController.adjust();
			}
		}
	}

	hide(el) {
		if (el === this.visible) {
			this.visible.dataset.interacted = true;
			this.visible.classList.remove('is-visible');
			this.visible = null;
			document.documentElement.style.setProperty('--bottom-panel-height', '0px');
			ScrollController.adjust();
			if (this.queue[0]) {
				this.show(this.queue[0]);
				this.queue.shift();
			}
		}
	}

}

export default new PopUpController();
