import FastClick from 'fastclick';

import AnimationController from './controllers/animation';
import AssetController from './controllers/asset';
import ColorController from './controllers/color';
import HeightController from './controllers/height';
import OverlayController from './controllers/overlay';
import PageController from './controllers/page';
import ResizeController from './controllers/resize';
// import RevealController from './controllers/reveal';
import RoutingController from './controllers/routing';
import ScrollController from './controllers/scroll';
import SectionController from './controllers/section';
import SoundcloudController from './controllers/soundcloud';

// use to find sections
import Accordion from '../../modules/accordion/script';
import GridWorkFilter from '../../modules/grids/work-filter/script';
import Header from '../../modules/header/script';
import ListBlogFilter from '../../modules/lists/blog-filter/script';
import ListThinkingFilter from '../../modules/lists/thinking-filter/script';

import { getRootOffset } from './helpers';

class App {

	el = {};

	isIframe = false;
	isInitialized = false;
	isTouch = false;

	page;

	nextAsset;

	constructor() {
		this.isIframe = window.location.pathname.includes('/styleguide');

		this.addEventListeners();
	}

	gatherElements() {
		this.el = {
			menu: document.querySelector('.menu'),
			main: document.querySelector('main.smooth-scroll'),
			content: document.querySelector('.smooth-scroll__content'),
			overlays: document.querySelector('.overlays'),
			playerSticky: document.querySelector('.player-sticky')
		};
	}

	configureControllers() {
		ResizeController.initialize();

		HeightController.initialize();

		PageController.initialize(this.el.content);

		// const { pending, done, failed } = SectionController.status;
		// SectionController.monit = [pending, done, failed];
		SectionController.initialize(this.el.content);
		SectionController.seekInitialize();

		ScrollController.initialize(this.el.main, [
			'[data-uri] > section',
			'.footer'
		]);

		// const { loading, rendered, removed } = AssetController.status;
		// AssetController.monit = [loading, rendered, removed];
		AssetController.initialize();

		ColorController.initialize();

		OverlayController.initialize(this.el.overlays);
		OverlayController.add({
			menu: this.el.menu
		});

		SoundcloudController.initialize(this.el.playerSticky);

		AnimationController.start();

		RoutingController.start({
			// '/404': async () => {
			// 	await this.open('404');
			// },
			'/': async () => {
				await this.open('home');
			},
			'/:page': async (page) => {
				await this.open(page);
			},
			'/:channel/:page': async (channel, page) => {
				await this.open(`${channel}/${page}`);
			},

			'/about/awards': async () => {
				await this.open('about');
				OverlayController.open('about/awards');
			},
			'/about/:member': async (member) => {
				await this.open('about');
				OverlayController.open('about/team', member);
			},

			'/case-studies': async () => {
				await this.open('case-studies');
				SectionController.class(GridWorkFilter).then((cl) => cl.filter());
				RoutingController.restoreProgress('case-studies');
			},
			'/case-studies/:category/:option': async (category, option) => {
				await this.open('case-studies');
				SectionController.class(GridWorkFilter).then((cl) => cl.filter(`${category}/${option}`));
				RoutingController.restoreProgress('case-studies');
			},

			'/blog': async () => {
				await this.open('blog');
				SectionController.class(ListBlogFilter).then((cl) => cl.filter('all'));
				RoutingController.restoreProgress('blog');
			},
			'/blog/categories/:category': async (category) => {
				await this.open('blog');
				SectionController.class(ListBlogFilter).then((cl) => cl.filter(category));
				RoutingController.restoreProgress('blog');
			},

			'/thinking': async () => {
				await this.open('thinking');
				SectionController.class(ListThinkingFilter).then((cl) => cl.filter('all'));
				RoutingController.restoreProgress('thinking');
			},
			'/thinking/categories/:category': async (category) => {
				await this.open('thinking');
				SectionController.class(ListThinkingFilter).then((cl) => cl.filter(category));
				RoutingController.restoreProgress('thinking');
			},

			'/careers': async () => {
				await this.open('careers');
				SectionController.class(Accordion).then((cl) => cl.filter());
			},
			'/careers/:office/?:department': async (office, department) => {
				if (Accordion.offices.includes(office)) {
					await this.open('careers');
					SectionController.class(Accordion).then((cl) => cl.filter(office, department));
				} else {
					await this.open(`careers/${office}`);
				}
			}

			// '/page/:a/?:b': (a, b) => console.log(a, b)
		});
	}

	activate(uri) {
		let page = uri.split('/')[0];
		if (page === 'industries' || page === 'case-studies') page = 'services';
		SectionController.class(Header).then((cl) => cl.activate(page));
	}

	freeze() {
		ScrollController.lock();
		if (!this.isTouch) ScrollController.rendering = false;
		document.documentElement.classList.remove('is-ready');
	}

	thaw() {
		ScrollController.unlock();
		if (!this.isTouch) ScrollController.rendering = true;
		document.documentElement.classList.add('is-ready');
	}

	addFade() {
		document.documentElement.classList.add('is-fading');
	}

	removeFade() {
		document.documentElement.classList.remove('is-fading');
	}

	async open(uri) {
		if (window.ga) window.ga('send', 'pageview');

		this.freeze();

		OverlayController.closeAll();

		if (!this.isInitialized) {
			this.isInitialized = true;
			// TODO remove page reference
			this.page = PageController.detect();
			// if (PageController.active !== uri) this.open('404');
		} else {
			const { active } = PageController;
			if (uri === active) {
				this.thaw();
				return;
			}

			// remove next case study override on landing pages
			if (uri.split('/').length === 1) {
				document.documentElement.classList.remove('is-next-case-study');
			}

			RoutingController.setProgress(active, ScrollController.eased);
			document.documentElement.classList.add('is-new-page');
			this.page = await PageController.open(uri);
			SectionController.destroyPage(active);
			PageController.close(active);
			RoutingController.restoreProgress(uri);
		}

		OverlayController.removeSlug();

		const introAsset = document.querySelector('[data-intro-asset] .asset');
		const introAssetSection = introAsset && introAsset.closest('section');
		const delayIntroImage = () => new Promise((resolve) => {
			if (introAsset) {
				document.documentElement.style.setProperty('--intro-asset-offset', `${getRootOffset(introAsset).top}px`);

				let isLoaded = false;
				introAsset.addEventListener('load', () => {
					isLoaded = true;
					resolve();
				});

				// fallback for missed asset loads
				setTimeout(() => {
					if (!isLoaded) {
						resolve();
					}
				}, 100);
			} else {
				resolve();
			}
		});

		const delayIntroLogo = () => new Promise((resolve) => {
			const takeoverLogo = document.querySelector('.logo-takeover__logo');
			if (takeoverLogo && !takeoverLogo.classList.contains('is-resolved')) {
				// takeoverLogo.addEventListener('animationend', resolve);
				takeoverLogo.classList.add('is-resolved');
				resolve();
			} else {
				resolve();
			}
		});

		delayIntroImage().then(() => {
			delayIntroLogo().then(() => {
				document.documentElement.classList.add('is-resolved');

				this.thaw();

				if (introAssetSection) {
					introAssetSection.classList.add('is-ready');
				}

				const maskHeading = document.querySelector('[data-mask-heading]');
				const maskHeadingContact = document.querySelector('[data-mask-heading-contact]');
				if (maskHeading) {
					const isNewPage = document.documentElement.classList.contains('is-new-page');
					const isCaseStudy = document.body.getAttribute('data-page-type') === 'caseStudies';
					const section = maskHeading.closest('section');
					const words = maskHeading.querySelectorAll('.word, .dot-wrapped');
					let delay = false;
					let last = 0;
					for (const word of words) {
						const offset = getRootOffset(word).top;
						if (offset > last) {
							last = offset;
							if (delay === false) {
								if (isNewPage && !isCaseStudy) {
									delay = 0.25;
								} else if (!isNewPage) {
									delay = 0.6;
								} else {
									delay = 0;
								}
							} else {
								delay += 0.1;
							}
						}
						for (const span of word.querySelectorAll('span')) {
							span.style.animationDelay = `${delay}s`;
						}

						if (maskHeadingContact) {
							const dot = maskHeadingContact.querySelector('.dot-wrapped span');
							dot.style.animationDelay = `${delay}s`;
						}
					}
					setTimeout(() => {
						section.classList.add('is-ready');
					}, delay * 1000);
				}

				setTimeout(() => {
					this.addFade();
				}, 250);
			});
		});

		this.activate(uri);

		this.apply(this.page, uri);
		this.refresh(true);
	}

	apply(el, uri) {
		RoutingController.parseEventTracking();
		RoutingController.overrideAnchors();
		SectionController.seekInitialize(el, uri);
	}

	destroy(el) {
		SectionController.seekDestroy(el);
		this.refresh(true);
	}

	refresh(gather) {
		ScrollController.refresh(gather);
		ColorController.refresh(gather);
		AssetController.refresh(gather);
	}

	#onDOMContentLoaded = () => {
		FastClick.attach(document.body);
		if (!this.isIframe) {
			this.gatherElements();
			this.configureControllers();
		}
	}

	#onDocumentTouchStart = () => {
		if (!this.isTouch) {
			this.isTouch = true;
			document.documentElement.classList.add('is-touch');
			document.removeEventListener('touchstart', this.#onDocumentTouchStart);
			ScrollController.rendering = false;
			ScrollController.ease.duration = 20;
			ScrollController.removeStyles();
		}
	}

	addEventListeners() {
		document.addEventListener('DOMContentLoaded', this.#onDOMContentLoaded);
		document.addEventListener('touchstart', this.#onDocumentTouchStart, { passive: true });
	}

	removeEventListeners() {
		document.removeEventListener('DOMContentLoaded', this.#onDOMContentLoaded);
		document.removeEventListener('touchstart', this.#onDocumentTouchStart);
	}

}

export default new App();
