import AnimationController from './animation';
import HeightController from './height';
import ResizeController from './resize';
import ScrollController from './scroll';

import { getRootOffset } from '../helpers';

class AssetController {

	selector = '.asset';

	attributes = {
		src: 'data-src'
	};

	status = {
		loading: 'loading',
		done: 'done',
		failed: 'failed',
		rendered: 'rendered',
		removed: 'removed'
	};

	colors = {
		[this.status.loading]: '#ffff00',
		[this.status.done]: '#00ff00',
		[this.status.failed]: '#ff0000',
		[this.status.rendered]: '#00ffff',
		[this.status.removed]: '#666666'
	};

	amimation;

	monit = false;

	// observer;

	assets = [];

	cache = {};

	initialize() {
		this.animation = AnimationController.set({
			update: this.update,
			render: this.render
		});

		ResizeController.add(this.#onResize);
		HeightController.add(this.#onResize);

		// TODO add fallback observer for robots
		// this.observer = new IntersectionObserver(this.#onObservation);
	}

	#setAssetStatus(asset, status) {
		asset.status = status;
		if (this.monit && this.monit.includes(status)) {
			console.log(`${asset.src} [${asset.natural[0]}, ${asset.natural[1]}] %c ${asset.status}`, `color: ${this.colors[asset.status]}`); // eslint-disable-line no-console
		}
	}

	gather() {
		this.assets = [];
		const assets = document.querySelectorAll(`${this.selector}`);
		for (const container of assets) {
			const svg = container.querySelector('svg');
			const figure = container.querySelector(`[${this.attributes.src}]`);
			if (figure) {
				const index = this.assets.length;
				container.dataset.index = index;
				this.assets.push({
					container,
					svg,
					figure,
					asset: container.querySelector('img') || container.querySelector('video') || null,
					natural: [0, 0],
					dimensions: [figure.clientWidth, figure.clientHeight],
					type: figure.dataset.type,
					src: figure.dataset.src,
					alt: figure.dataset.alt,
					offset: 0,
					render: container.dataset.render,
					status: null,
				});
				// this.observer.observe(container);
			}
		}
	}

	measure() {
		for (const asset of this.assets) {
			asset.offset = getRootOffset(asset.container).top;
			if (asset.svg) asset.dimensions = [asset.svg.clientWidth, asset.svg.clientHeight];
		}
	}

	refresh(gather) {
		if (gather) {
			// this.observer.disconnect();
			this.gather();
		}

		this.measure();
	}

	image(asset) {
		asset.asset = document.createElement('img');

		asset.asset.setAttribute('alt', asset.alt);

		asset.asset.src = asset.src;

		if (asset.asset.decode && asset.src.substr(asset.src.length - 3) !== 'svg') {
			asset.asset.decode().then(() => {
				asset.natural = [asset.asset.naturalWidth, asset.asset.naturalHeight];
				if (asset.status === this.status.loading) this.#setAssetStatus(asset, this.status.done);
			}).catch(() => {
				// console.warn(e); // eslint-disable-line no-console
			});
		} else {
			asset.asset.onerror = () => {
				// console.warn(e); // eslint-disable-line no-console
			};
			asset.asset.onload = () => {
				asset.natural = [asset.asset.naturalWidth, asset.asset.naturalHeight];
				if (asset.status === this.status.loading) this.#setAssetStatus(asset, this.status.done);
			};
		}
	}

	video(asset) {
		asset.asset = document.createElement('video');

		asset.asset.onerror = () => {
			// console.warn(e);
		};
		asset.asset.oncanplay = () => {
			asset.natural = [asset.asset.videoWidth, asset.asset.videoHeight];
			if (asset.status === this.status.loading) {
				this.#setAssetStatus(asset, this.status.done);
				asset.asset.play().catch(() => {
					// console.warn(e);
				});
			}
			if (!this.cache[asset.src]) this.cache[asset.src] = asset.natural;
		};

		asset.asset.setAttribute('alt', asset.alt);

		asset.asset.setAttribute('playsinline', true);
		asset.asset.setAttribute('muted', true);
		// asset.asset.setAttribute('autoplay', true);
		asset.asset.setAttribute('loop', true);
		asset.asset.setAttribute('preload', 'metadata');
		// wtf
		asset.asset.src = asset.src;
		asset.asset.playsinline = true;
		asset.asset.muted = true;
		// asset.asset.autoplay = true;
		asset.asset.loop = true;
		asset.asset.preload = 'metadata';

		asset.asset.load();
	}

	update = () => {
		let render = false;
		for (const asset of this.assets) {
			if (asset.status === null && !asset.asset && ((ScrollController.eased + ScrollController.viewport > asset.offset && ScrollController.eased < asset.offset + asset.dimensions[1]) || asset.render)) {
				this.#setAssetStatus(asset, this.status.loading);
				if (this[asset.type]) this[asset.type](asset);
			}
			if (asset.status === this.status.done) render = true;
			if (asset.type === 'video' && asset.status !== null && (ScrollController.eased + ScrollController.viewport < asset.offset || ScrollController.eased > asset.offset + asset.dimensions[1])) {
				this.#setAssetStatus(asset, this.status.removed);
				render = true;
			}
		}
		return render;
	}

	render = () => {
		for (const asset of this.assets) {
			if (asset.status === this.status.done) {
				this.#setAssetStatus(asset, this.status.rendered);
				if (!asset.svg) {
					asset.svg = document.createRange().createContextualFragment(`<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 ${asset.natural[0]} ${asset.natural[1]}" width="${asset.natural[0]}" height="${asset.natural[1]}" preserveAspectRatio></svg>`);
					asset.figure.parentNode.insertBefore(asset.svg, asset.figure);
					asset.svg = asset.container.querySelector('svg');
					asset.dimensions = [asset.svg.clientWidth, asset.svg.clientHeight];
					ScrollController.adjust();
				}
				asset.figure.parentNode.appendChild(asset.asset);
				const e = new CustomEvent('load', { detail: { asset } });
				asset.container.dispatchEvent(e);
			}
			if (asset.status === this.status.removed) {
				this.#setAssetStatus(asset, null);
				asset.asset.src = '';
				asset.asset.load();
				asset.asset.remove();
				delete asset.asset;
			}
		}
	}

	// #onObservation = (entries) => {
	// 	for (const entry of entries) {
	// 		const asset = this.assets[entry.target.dataset.index];
	// 		if (entry.isIntersecting) {
	// 			if (!asset.asset) {
	// 				const type = asset.type.charAt(0).toUpperCase() + asset.type.slice(1);
	// 				this[`load${type}`](asset);
	// 			}
	// 		} else if (asset.type === 'video' && asset.asset) {
	// 			asset.asset.src = '';
	// 			asset.asset.load();
	// 			asset.asset.remove();
	// 			delete asset.asset;
	// 		}
	// 	}
	// }

	#onResize = () => {
		this.measure();
	}

}

export default new AssetController();
