import RoutingController from './routing';
import ScrollController from './scroll';
import SectionController from './section';

class OverlayController {

	attributes = {
		close: 'data-overlay-close',
		slug: 'data-overlay-slug'
	};

	classes = {
		isOpen: 'is-open',
		isClosed: 'is-closed'
	};

	container;

	overlays = {};

	initialize(container) {
		this.container = container;

		this.addEventListeners();
	}

	add(obj) {
		for (const id in obj) {
			const el = obj[id];
			const slug = el.dataset.overlaySlug;
			const section = SectionController.find(el);
			this.overlays[id] = {
				el,
				slug,
				section,
				open: false
			};
			const close = el.querySelector(`[${this.attributes.close}]`);
			if (close) close.addEventListener('click', () => this.close(id));
		}
	}

	remove(id) {
		if (this.overlays[id].open) this.close(id);
		delete this.overlays[id];
	}

	find(slug) {
		const overlay = document.querySelector(`[${this.attributes.slug}="${slug}"]`);
		if (overlay) {
			this.add({ [slug]: overlay });
			this.container.appendChild(overlay);
		}
	}

	open(id, content) {
		if (!this.overlays[id]) this.find(id);

		this.overlays[id].el.classList.remove(this.classes.isClosed);
		this.overlays[id].el.classList.add(this.classes.isOpen);
		this.overlays[id].open = true;

		if (this.overlays[id].section && this.overlays[id].section.class.open) {
			this.overlays[id].section.class.open(content);
		}

		ScrollController.lock();
	}

	close(id) {
		this.overlays[id].el.classList.remove(this.classes.isOpen);
		this.overlays[id].el.classList.add(this.classes.isClosed);
		this.overlays[id].open = false;

		if (this.overlays[id].section && this.overlays[id].section.class.close) {
			this.overlays[id].section.class.close();
		}

		ScrollController.unlock();
	}

	closeSlug(id) {
		const { slug } = this.overlays[id];
		if (slug) {
			const url = slug.substr(0, slug.lastIndexOf('/'));
			RoutingController.navigate(`/${url}`);
		} else {
			this.close(id);
		}
	}

	closeAll() {
		for (const id in this.overlays) {
			if (this.overlays[id].open) this.close(id);
		}
	}

	removeSlug() {
		for (const id in this.overlays) {
			if (this.overlays[id].slug) {
				this.overlays[id].el.innerHTML = '';
				this.overlays[id].el.remove();
				delete this.overlays[id];
			}
		}
	}

	#onDocumentKeyDown = (e) => {
		if (e.keyCode === 27) {
			for (const id in this.overlays) {
				if (this.overlays[id].open) this.closeSlug(id);
			}
		}
	}

	#onContainerClick = (e) => {
		for (const id in this.overlays) {
			if (this.overlays[id].open && this.overlays[id].el === e.target) {
				this.closeSlug(id);
			}
		}
	}

	addEventListeners() {
		document.addEventListener('keydown', this.#onDocumentKeyDown);

		this.container.addEventListener('click', this.#onContainerClick);
	}

	removeEventListeners() {
		document.removeEventListener('keydown', this.#onDocumentKeyDown);

		this.container.removeEventListener('click', this.#onContainerClick);
	}

}

export default new OverlayController();
