import PageController from '../../../assets/js/controllers/page';
import SoundcloudController from '../../../assets/js/controllers/soundcloud';

import Waveform from '../../../assets/js/components/waveform';

export default class PlayerSoundcloud {

	static selector = 'player-soundcloud';

	classes = {
		isVisible: 'is-visible',
		isPlaying: 'is-playing'
	};

	el = {};

	data;

	player;

	constructor(container, uri) {
		this.el = {
			container,
			toggle: container.querySelector(`.${this.constructor.selector}__toggle`),
			waveform: container.querySelector(`.${this.constructor.selector}__waveform`),
			current: container.querySelector(`.${this.constructor.selector}__current`),
			duration: container.querySelector(`.${this.constructor.selector}__duration`),

			listenTime: document.querySelector('[data-listen-time]')
		};

		this.data = PageController.data[uri];
	}

	async initialize() {
		this.player = await SoundcloudController.requestPlayer(this.data.urlSoundcloudPlayer);
		SoundcloudController.setCover(this.data.urlSoundcloudPlayer, this.data.asset[0].url);
		if (this.player) {
			this.el.waveform.classList.add(this.classes.isVisible);
			const minutes = Math.round(this.player.track.duration / 1000 / 60);
			this.el.listenTime.innerText = `${minutes} Min`;

			this.player.waveform = new Waveform(this.player.track.waveform_url);
			this.player.waveform.renderHTML(this.el.waveform);
			this.player.waveform.onseeking = (progress) => {
				if (this.player.audio.duration) this.player.audio.currentTime = this.player.audio.duration * progress;
			};

			if (!this.player.audio.paused) this.el.container.classList.add(this.classes.isPlaying);
			this.addEventListeners();
		}
	}

	play() {
		this.player.audio.play();
	}

	pause() {
		this.player.audio.pause();
	}

	#onToggleClick = () => {
		this.player.audio.paused ? this.play() : this.pause();
	}

	#onPlayerPlay = () => {
		this.el.container.classList.add(this.classes.isPlaying);
	}

	#onPlayerPause = () => {
		this.el.container.classList.remove(this.classes.isPlaying);
	}

	#onPlayerLoadedMetaData = () => {
		const duration = SoundcloudController.formatSeconds(this.player.audio.duration);
		this.el.duration.innerText = `/${duration}`;
	}

	#onPlayerTimeUpdate = () => {
		this.player.waveform.setProgress(this.player.audio.currentTime / this.player.audio.duration);
		this.el.current.innerText = SoundcloudController.formatSeconds(this.player.audio.currentTime);
		const duration = SoundcloudController.formatSeconds(this.player.audio.duration);
		this.el.duration.innerText = `/${duration}`;
	}

	addEventListeners() {
		this.el.toggle.addEventListener('click', this.#onToggleClick);

		this.player.audio.addEventListener('play', this.#onPlayerPlay);
		this.player.audio.addEventListener('pause', this.#onPlayerPause);
		this.player.audio.addEventListener('loadedmetadata', this.#onPlayerLoadedMetaData);
		this.player.audio.addEventListener('timeupdate', this.#onPlayerTimeUpdate);
	}

	removeEventListeners() {
		this.el.toggle.removeEventListener('click', this.#onToggleClick);

		this.player.audio.removeEventListener('play', this.#onPlayerPlay);
		this.player.audio.removeEventListener('pause', this.#onPlayerPause);
		this.player.audio.removeEventListener('loadedmetadata', this.#onPlayerLoadedMetaData);
		this.player.audio.removeEventListener('timeupdate', this.#onPlayerTimeUpdate);
	}

	destroy() {
		this.player.waveform.destroy();
		this.removeEventListeners();
	}

}
