import Cursor from '../../../assets/js/components/cursor';


export default class IntroHome {

	static selector = 'intro-home';

	el = {};

	cursor;

	constructor(container) {
		this.el = {
			container,
			wrapper: container.querySelector(`.${this.constructor.selector}__wrapper`)
		};

		this.breakpoint = window.matchMedia('(min-width: 1280px)');

		if (this.breakpoint.matches) {
			this.cursor = new Cursor(this.el.wrapper, this.el.wrapper);
		}
	}

	destroy() {
		if (this.cursor) {
			this.cursor.destroy();
			this.cursor = null;
		}
	}

}
